
<template>
    <div class="container">
      
      <form @submit.prevent="addChauffeur" class="row form-form form-75">

        <div class="title">
          <h4>
            <span>Ajout</span> <span>Chauffeur</span>
          </h4>
          <div class="line">
          
          </div>
        </div>

        <h5>Informations  Chauffeur</h5>
        <div class="col-12   col-md-6">
          <b-form-group label="nom :">
            <b-form-input id="nom" v-model="new_chauffeur.nom" required></b-form-input>
          </b-form-group>
        </div>
        <div class="col-12 col-md-6">
          <b-form-group label="Prénom :">
            <b-form-input id="prenoms" v-model="new_chauffeur.prenoms" required></b-form-input>
          </b-form-group>
        </div>
      
        <div class="col-12">
          <b-form-group label="Email :">
            <b-form-input id="email" v-model="new_chauffeur.email" type="email" required></b-form-input>
          </b-form-group>
        </div>
        
        <div class="col-12">
          <b-form-group label="Téléphone :">
            <b-form-input v-model="new_chauffeur.tel" required></b-form-input>
          </b-form-group>
        </div>
       
      
     
    
        <b-button type="submit" variant="" class="btn btn-save">
          <font-awesome-icon :icon="['fas', 'floppy-disk']" />
          <span>Enregistrer</span> 
        </b-button>
      
      </form>
    </div>
  </template>
  
  <script>
  import axios from 'axios';

  
 export default {
    name: 'ChauffeurCreatePage',
    mounted() {
        //   this.maFonction("created");
    },
    props: {
         baseUrl: String
    },
   data() {
      return {

        baseUrlPage:`${this.$store.state.baseUrl}/api/transport_scolaire/chauffeurs/`,
          new_chauffeur: {
            nom: '',
            prenoms: '',
            email: '',
            tel: '',
        },

       };
    },
    methods: {
       
      addChauffeur() {


            // Créez un nouvel objet Project avec les valeurs du formulaire



            // Envoyer la requête POST à l'API avec les données du projet
            axios.post(this.baseUrlPage, this.new_chauffeur)
                .then(response => {
                // Traitez la réponse de l'API en conséquence
                console.log(response.data);


                this.$router.push('/transport_scolaire/chauffeur');
                })
                .catch(error => {
                // Gérez les erreurs de la requête
                   console.error(error);
                });
      
      
   
       }
  
    },




  }
</script>

