
<template>
  <div class="container">
    
    <form @submit.prevent="editBus" class="row form-form form-75">

      <div class="title">
        <h4>
          <span>Ajout</span> <span>Bus</span>
        </h4>
        <div class="line">
        
        </div>
      </div>

      <h5>Informations  Bus</h5>
      <div class="col-12   col-md-6">
        <b-form-group label="immatriculation :">
          <b-form-input id="immatriculation" v-model="edit_bus.immatriculation" required></b-form-input>
        </b-form-group>
        <div class="text-danger">

        </div>
      </div>
      <div class="col-12 col-md-6">
        <b-form-group label="nombre_place :">
          <b-form-input id="nombre_place" v-model="edit_bus.nbr_place" required></b-form-input>
        </b-form-group>
      </div>
    
      
     
      <div class="col-12 col-md-6">
        <b-form-group label="Chauffeur :">
          <b-form-select v-model="edit_bus.chauffeur" :options="chauffeurs" required></b-form-select>
        </b-form-group>
      </div>
      <div class="col-12 col-md-6">
        <b-form-group label="Type Vehicule :">
          <b-form-select v-model="edit_bus.type" :options="types" required></b-form-select>
        </b-form-group>
      </div>
      <div class="col-12 col-md-6">
        <b-form-group label="Coleur :">
          <b-form-input id="couleur" v-model="edit_bus.couleur" type="color" required></b-form-input>
        </b-form-group>
      </div>

     
    


    
  
      <b-button type="submit" variant="" class="btn btn-save">
        <font-awesome-icon :icon="['fas', 'floppy-disk']" />
        <span>Enregistrer</span> 
      </b-button>
    
    </form>
  </div>
</template>

<script>
import axios from 'axios';


export default {
  name: 'BusCreatePage',


 data() {
    return {
        url:'',
        baseUrlPage:`${this.$store.state.baseUrl}/api/transport_scolaire/buss/`,
        baseUrlType:`${this.$store.state.baseUrl}/api/transport_scolaire/typevehicules/`,
        baseUrlChauffeur:`${this.$store.state.baseUrl}/api/transport_scolaire/chauffeurs/`,

        edit_bus: {
          immatriculation: '',
          immatriculation_error:'',
          nbr_place: '',
          couleur: '',
          type: '',
          chauffeur: 'red',
      },
      types: [],
      chauffeurs: [],
         
     };
  },
  methods: {
    fecthData(page){
              // Effectuer la requête GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: true, // Valeur de recherche
                } 
                })
                .then(response => {
                   const data = response.data;
                   this.edit_bus = {
                    ...data,
                    type:data.type.pk,
                    chauffeur:data.chauffeur.pk,
                   }
                })
                .catch(error => {
                     console.error("Une erreur s'est candidate lors de la récupération des eleves:", error);
                });
          },
    fecthDataChauffeur(page){
            // Effectuer la requête GET vers l'API en utilisant Axios
            axios.get(page,{ params: {
                  paginate: false, // Valeur de recherche
            } 
            })
            .then(response => {
                const data = response.data;
                console.log(data)
                this.chauffeurs = data.map(a => ({ value: a.pk, text: a.repr }));
            })
            .catch(error => {
                  console.error("Une erreur s'est candidate lors de la récupération des chauffeurs:", error);
            });

    },
    fecthDataType(page){
            // Effectuer la requête GET vers l'API en utilisant Axios
            axios.get(page,{ params: {
                  paginate: false, // Valeur de recherche
            } 
            })
              .then(response => {
                 const data = response.data;
                 console.log(data)
                 this.types = data.map(a => ({ value: a.pk, text: a.repr }));
              })
              .catch(error => {
                   console.error("Une erreur s'est candidate lors de la récupération des types:", error);
              });
    },
    editBus() {
          // Créez un nouvel objet Project avec les valeurs du formulaire
          let error="Cette champ est requis"
          if (!this.edit_bus.immatriculation) {  
               this.edit_bus.immatriculation_error = error
               return
          }
          if (!this.edit_bus.nbr_place) {
              this.edit_bus.nbr_place_error=error
              return
          }
          if (!this.edit_bus.couleur) {
              this.edit_bus.couleurerror = error
              return
          }
          if (!this.edit_bus.type) {
              this.edit_bus.type_error = error
              return
          }
          if (!this.edit_bus.chauffeur) {
            this.edit_bus.chauffeur_error = error
            return
          }


          // Envoyer la requête POST à l'API avec les données du projet
          axios.put(this.url, this.edit_bus)
              .then(response => {
              // Traitez la réponse de l'API en conséquence
              console.log(response.data);
                   // Réinitialisez les valeurs du formulaire
   

              this.$router.push('/transport_scolaire/bus');
              })
            .catch(error => {
            // Gérez les erreurs de la requête
            console.error(error);
            });
    
    
 
     },

  },
  mounted() {
    const encryptedId = this.$route.params.id; // Récupère l'ID crypté depuis l'URL
    const decryptedId = this.$decryptId(encryptedId); // Décrypte l'ID
    console.log(decryptedId);

    this.url=`${this.$store.state.baseUrl}/api/transport_scolaire/buss/${decryptedId}/`
    
    this.fecthData(this.url);
    this.fecthDataType(this.baseUrlType);
    this.fecthDataChauffeur(this.baseUrlChauffeur);
  }




}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.popup-task{
  position: fixed;
  z-index: 100;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  width: 100%;
  max-width: 400px;
  background-color: #383838;
  padding: 20px;
  color: #fff;
  box-shadow: rgba(0, 0, 0, 0.1);
  border-radius: 5px;
}
h3 {
  margin: 40px 0 0;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>

