
<template>
  <div class="container">
    
    <form @submit.prevent="deleteLieuLigne" class="row form-form form-75">

      <div class="title">
        <h4>
          <span>Suppression</span> <span>LieuLigne</span>
           <p>{{edit_eleve.repr}}</p>
        </h4>
        <div v-if="!showDeleteAlert" class="alert alert-danger">
            Êtes-vous sûr de vouloir supprimer ce eleve ? Cette action est irréversible.
            <button class="btn btn-danger" @click="confirmDelete">Confirmer</button>
            <button class="btn btn-secondary" @click="cancelDelete">Annuler</button>
            

          </div>
        <div class="line">
        
        </div>
      </div>


    
    </form>
  </div>
</template>

<script>
import axios from 'axios';


export default {
  name: 'LieuLigneCreatePage',
  mounted() {
      const encryptedId = this.$route.params.id; // Récupère l'ID crypté depuis l'URL
      const decryptedId = this.$decryptId(encryptedId); // Décrypte l'ID
      console.log(decryptedId);

      this.url=`${this.$store.state.baseUrl}/api/transport_scolaire/eleves/${decryptedId}/`
      // console.log(url)
      this.fecthData(this.url)
  },

  data() {
  return {
    url:'',
    baseUrlPage:`${this.$store.state.baseUrl}/api/transport_scolaire/eleves/`,
    edit_eleve: {
      nom: '',
      prenoms: '',
      email: '',
      tel: "",
      repr:""
    
    },
  
  };
},
  methods: {
    fecthData(page){
            // Effectuer la requête GET vers l'API en utilisant Axios
            axios.get(page,{ params: {
                  paginate: true, // Valeur de recherche
              } 
              })
              .then(response => {
                 const data = response.data;
                 this.edit_eleve = {
                  ...data,
                 }
              })
              .catch(error => {
                   console.error("Une erreur s'est candidate lors de la récupération des eleves:", error);
              });
        },
        confirmDelete() {
          // Envoyer la requête POST à l'API avec les données du projet
          axios.delete(this.url, this.edit_eleve)
              .then(response => {
              // Traitez la réponse de l'API en conséquence
              console.log(response.data);
                   // Réinitialisez les valeurs du formulaire
              this.$router.push('/transport_scolaire/eleve');
              })
              .catch(error => {
              // Gérez les erreurs de la requête
              console.error(error);
              });
        },
        cancelDelete() {

              this.$router.push('/transport_scolaire/eleve');
        
        }

  },




}
</script>

