
<template>
    <div class="container">
      
      <form @submit.prevent="editBus" class="row form-form form-75">
  
        <div class="title">
          <h4>
            <span>Modification</span> <span>Bus</span>
          </h4>
          <div class="line">
          
          </div>
        </div>
  
        <h5>Informations  Bus</h5>
        <div class="col-12   col-md-6">
          <b-form-group label="nom :">
            <b-form-input id="nom" v-model="new_bus.nom" required></b-form-input>
          </b-form-group>
        </div>
        <div class="col-12 col-md-6">
          <b-form-group label="Prénom :">
            <b-form-input id="prenoms" v-model="new_bus.prenoms" required></b-form-input>
          </b-form-group>
        </div>
      
        <div class="col-12">
          <b-form-group label="Date Naissance :">
            <b-form-input id="date_naissance" v-model="new_bus.date_naissance" type="date" required></b-form-input>
          </b-form-group>
        </div>
        <div class="col-12 col-md-6">
          <b-form-group label="Ecole :">
            <b-form-select v-model="new_bus.ecole" :options="ecoles" required></b-form-select>
          </b-form-group>
        </div>
        <div class="col-12 col-md-6">
          <b-form-group label="Classe :">
            <b-form-select v-model="new_bus.classe" :options="classes" required></b-form-select>
          </b-form-group>
        </div>
        <div class="col-12">
          <b-form-group label="Adresse :">
            <b-form-input v-model="new_bus.adresse" required></b-form-input>
          </b-form-group>
        </div>
        <div class="col-12 col-md-6">
          <b-form-group label="Ligne de bus :">
            <b-form-select v-model="new_bus.ligne_bus" :options="lignes_bus" required></b-form-select>
          </b-form-group>
        </div>
        <div class="col-12 col-md-6">
          <b-form-group label="Lieu de ligne :">
            <b-form-select v-model="new_bus.lieu_ligne" :options="lieux_ligne" required></b-form-select>
          </b-form-group>
        </div>
      
        <div class="col-12">
          <b-form-group label="Lieu de ramassage :">
            <b-form-select v-model="new_bus.lieu_ramassage" :options="lieux_ramassage" required></b-form-select>
          </b-form-group>
        </div>
        <h5>Informations Parent Bus</h5>
      <div class="col-12 col-md-6">
          <b-form-group label="Nom Père :">
            <b-form-input v-model="new_bus.nom_pere" required></b-form-input>
          </b-form-group>
      </div>
      <div class="col-12 col-md-6">
      
          <b-form-group label="Tel :">
            <b-form-input v-model="new_bus.tel_pere" required></b-form-input>
          </b-form-group>
      </div>
      <div class="col-12 col-md-6">
          <b-form-group label="Nom Mère :">
            <b-form-input v-model="new_bus.nom_pere" required></b-form-input>
          </b-form-group>
      </div>
      <div class="col-12 col-md-6">
        <b-form-group label="Tel :">
          <b-form-input v-model="new_bus.tel_pere" required></b-form-input>
        </b-form-group>
      </div>
     
    
        <b-button type="submit" variant="" class="btn btn-save">
          <font-awesome-icon :icon="['fas', 'floppy-disk']" />
          <span>Enregistrer les modifications</span> 
        </b-button>
      
      </form>
    </div>
  </template>
  
  <script>
  import axios from 'axios';
  const baseUrl="localhost:8000"
  
  export default {
    name: 'BusCreatePage',
    mounted() {
        //   this.maFonction("created");
    },
  
    data() {
    return {
      new_bus: {
        nom: 'Doe',
        prenoms: 'John',
        date_naissance: '2005-08-15',
        ecole: 2,
        niveau: 'Niveau 3',
        classe: 3,
        ligne_bus: 1,
        lieu_ligne: 'Lieu 2',
        lieu_ramassage: 'Lieu 1',
        nom_pere: 'Doe',
        tel_pere: '0123456789',
        nom_mere: 'Smith',
        tel_mere: '9876543210',
        adresse: '123 Rue Principale'
      },
      ecoles: [
        { value: 1, text: 'Ecole 1' },
        { value: 2, text: 'Ecole 2' },
        { value: 3, text: 'Ecole 3' }
      ],
      classes: [
        { value: 1, text: 'Classe 1' },
        { value: 2, text: 'Classe 2' },
        { value: 3, text: 'Classe 3' }
      ],
      lignes_bus: [
        { value: 1, text: 'Ligne 1' },
        { value: 2, text: 'Ligne 2' },
        { value: 3, text: 'Ligne 3' }
      ],
      lieux_ligne: [
        { value: 'Lieu 1', text: 'Lieu 1' },
        { value: 'Lieu 2', text: 'Lieu 2' },
        { value: 'Lieu 3', text: 'Lieu 3' }
      ],
      lieux_ramassage: [
        { value: 'Lieu 1', text: 'Lieu 1' },
        { value: 'Lieu 2', text: 'Lieu 2' },
        { value: 'Lieu 3', text: 'Lieu 3' }
      ]
    };
  },
    methods: {
       
      editBus() {
  
  
            // Créez un nouvel objet Project avec les valeurs du formulaire
            const new_bus = {
                nom: this.new_bus.nom,
                agge: this.new_bus.agge,
            };
            this.$router.push('/bus');
  
  
            // Envoyer la requête POST à l'API avec les données du projet
            axios.post(`http://${baseUrl}/api/bus`, new_bus)
                .then(response => {
                // Traitez la réponse de l'API en conséquence
                console.log(response.data);
                     // Réinitialisez les valeurs du formulaire
          this.new_bus.nom = null ;
          this.new_bus.agge = null ;
  
                this.$router.push('/Bus');
                })
                .catch(error => {
                // Gérez les erreurs de la requête
                console.error(error);
                });
      
      
   
       }
  
    },
  
  
  
  
  }
  </script>
  
  <!-- Add "scoped" attribute to limit CSS to this component only -->
  <style scoped>
  .popup-task{
    position: fixed;
    z-index: 100;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 100%;
    max-width: 400px;
    background-color: #383838;
    padding: 20px;
    color: #fff;
    box-shadow: rgba(0, 0, 0, 0.1);
    border-radius: 5px;
  }
  h3 {
    margin: 40px 0 0;
  }
  ul {
    list-style-type: none;
    padding: 0;
  }
  li {
    display: inline-block;
    margin: 0 10px;
  }
  a {
    color: #42b983;
  }
