
<template>
  <div class="container">
    
    <form @submit.prevent="editEleve" class="row form-form form-75">

      <div class="title">
        <h4>
          <span>Modification</span> <span>Elève</span>
        </h4>
        <div class="line">
        
        </div>
      </div>

      <h5>Informations  Elève</h5>
      <div class="col-12   col-md-6">
        <b-form-group label="nom :">
          <b-form-input id="nom" v-model="edit_eleve.nom" required></b-form-input>
        </b-form-group>
      </div>
      <div class="col-12 col-md-6">
        <b-form-group label="Prénom :">
          <b-form-input id="prenoms" v-model="edit_eleve.prenoms" required></b-form-input>
        </b-form-group>
      </div>
    
      <div class="col-12">
        <b-form-group label="Date Naissance :">
          <b-form-input id="date_naissance" v-model="edit_eleve.date_naissance" type="date" required></b-form-input>
        </b-form-group>
      </div>
      <div class="col-12 col-md-6">
        <b-form-group label="Ecole :">
          <b-form-select v-model="edit_eleve.ecole" :options="ecoles" required></b-form-select>
        </b-form-group>
      </div>
      <div class="col-12 col-md-6">
        <b-form-group label="Classe :">
          <b-form-select v-model="edit_eleve.classe" :options="classes" required></b-form-select>
        </b-form-group>
      </div>
      <div class="col-12">
        <b-form-group label="Adresse :">
          <b-form-input v-model="edit_eleve.adresse" required></b-form-input>
        </b-form-group>
      </div>
      <div class="col-12 col-md-6">
        <b-form-group label="Ligne de bus :">
          <b-form-select v-model="edit_eleve.ligne" :options="lignes" required></b-form-select>
        </b-form-group>
      </div>
      <div class="col-12 col-md-6">
        <b-form-group label="Lieu de ligne :">
          <b-form-select v-model="edit_eleve.lieuligne" :options="lieulignes" required></b-form-select>
        </b-form-group>
      </div>
    
      <div class="col-12">
        <b-form-group label="Lieu de ramassage :">
          <b-form-select v-model="edit_eleve.lieu_ramassage" :options="lieuramassages" required></b-form-select>
        </b-form-group>
      </div>
      <h5>Informations Parent Elève</h5>
    <div class="col-12 col-md-6">
        <b-form-group label="Nom Père :">
          <b-form-input v-model="edit_eleve.parent.nom_pere" required></b-form-input>
        </b-form-group>
    </div>
    <div class="col-12 col-md-6">
    
        <b-form-group label="Tel :">
          <b-form-input v-model="edit_eleve.parent.tel_pere" required></b-form-input>
        </b-form-group>
    </div>
    <div class="col-12 col-md-6">
        <b-form-group label="Nom Mère :">
          <b-form-input v-model="edit_eleve.parent.nom_pere" required></b-form-input>
        </b-form-group>
    </div>
    <div class="col-12 col-md-6">
      <b-form-group label="Tel :">
        <b-form-input v-model="edit_eleve.parent.tel_pere" required></b-form-input>
      </b-form-group>
    </div>
   
  
      <b-button type="submit" variant="" class="btn btn-save">
        <font-awesome-icon :icon="['fas', 'floppy-disk']" />
        <span>Enregistrer les modifications</span> 
      </b-button>
    
    </form>
  </div>
</template>

<script>
import axios from 'axios';


export default {
  name: 'EleveEditPage',
  mounted() {
    const encryptedId = this.$route.params.id; // Récupère l'ID crypté depuis l'URL
    const decryptedId = this.$decryptId(encryptedId); // Décrypte l'ID
    console.log(decryptedId);

    this.url=`${this.$store.state.baseUrl}/api/transport_scolaire/eleves/${decryptedId}/`
    // console.log(url)
    this.fecthData(this.url)
    this.fecthDataEcole(this.baseUrlEcole);
    this.fecthDataClasse(this.baseUrlClasse);
    this.fecthDataLigne(this.baseUrlLigne);
    this.fecthDataLieuLigne(this.baseUrlLieuLigne);
    this.fecthDataLieuRamassage(this.baseUrlLieuRamassage);
  },

  data() {
  return {
          
          url:'',
          baseUrlPage:`${this.$store.state.baseUrl}/api/transport_scolaire/eleves/`,
          baseUrlEcole:`${this.$store.state.baseUrl}/api/transport_scolaire/ecoles/`,
          baseUrlClasse:`${this.$store.state.baseUrl}/api/transport_scolaire/classes/`,
          baseUrlLigne:`${this.$store.state.baseUrl}/api/transport_scolaire/lignes/`,
          baseUrlLieuLigne:`${this.$store.state.baseUrl}/api/transport_scolaire/lieulignes/`,
          baseUrlLieuRamassage:`${this.$store.state.baseUrl}/api/transport_scolaire/lieuramassages/`,

          edit_eleve: {
                nom: '',
                prenoms: '',
                date_naissance: '',
                ecole: '',
                niveau: '',
                classe: '',
                ligne: '',
                lieuligne: '',
                lieu_ramassage: '',
                parent:{
                    nom_mere:'',nom_pere:'',
                    tel_pere:'',tel_mere:''
                }
           },
           
        ecoles: [],
        classes: [],
        lignes: [],
        lieulignes: [],
        lieuramassages: [],
  };
},
  methods: {
    

    fecthData(page){
              // Effectuer la requête GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: true, // Valeur de recherche
                } 
                })
                .then(response => {
                   const data = response.data;
                   this.edit_eleve = {
                    ...data,
                    ecole: data.ecole.pk,
                    classe: data.classe.pk,
                    ligne: data.ligne.pk,
                    lieu_ramassage: data.lieu_ramassage.pk,
                    lieuligne: data.lieu_ramassage.lieu_ligne.pk,
                    // user:user,
                    // image:image
                  };
                  delete this.edit_eleve.user;
                  delete this.edit_eleve.image;
                 
                                
                })
                .catch(error => {
                     console.error("Une erreur s'est candidate lors de la récupération des eleves:", error);
                });
          },
    editEleve() {


          // Créez un nouvel objet Project avec les valeurs du formulaire
          // Envoyer la requête POST à l'API avec les données du projet
          axios.put(this.url, this.edit_eleve)
              .then(response => {

                response
              this.$router.push('/transport_scolaire/eleve/');
              })
              .catch(error => {
              // Gérez les erreurs de la requête
              console.error(error);
              });
    
    
 
     }

 ,
  fecthDataEcole(page){
              // Effectuer la requête GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: false, // Valeur de recherche
                } 
                })
                .then(response => {
                   const data = response.data;
                   this.ecoles = data.map(a => ({ value: a.pk, text: a.repr }));
                })
                .catch(error => {
                     console.error("Une erreur s'est candidate lors de la récupération des ecoles:", error);
                });
          },
          fecthDataClasse(page){
              // Effectuer la requête GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: false, // Valeur de recherche
                } 
                })
                .then(response => {
                   const data = response.data;
                   this.classes = data.map(a => ({ value: a.pk, text: a.repr }));
                })
                .catch(error => {
                     console.error("Une erreur s'est candidate lors de la récupération des classes:", error);
                });
          },
          fecthDataLigne(page){
              // Effectuer la requête GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: false, // Valeur de recherche
                } 
                })
                .then(response => {
                   const data = response.data;
                   this.lignes = data.map(a => ({ value: a.pk, text: a.repr }));
                })
                .catch(error => {
                     console.error("Une erreur s'est produit lors de la récupération des lignes:", error);
                });
          },
          fecthDataLieuLigne(page){
              // Effectuer la requête GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: false, // Valeur de recherche
                } 
                })
                .then(response => {
                   const data = response.data;
                   this.lieulignes = data.map(a => ({ value: a.pk, text: a.repr }));
                })
                .catch(error => {
                     console.error("Une erreur s'est candidate lors de la récupération des lignes:", error);
                });
          },
          fecthDataLieuRamassage(page){
              // Effectuer la requête GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: false, // Valeur de recherche
                } 
                })
                .then(response => {
                   const data = response.data;
                   this.lieuramassages = data.map(a => ({ value: a.pk, text: a.repr }));
                })
                .catch(error => {
                     console.error("Une erreur s'est candidate lors de la récupération des lieu de ramassages:", error);
                });
          },
  
    },






}
</script>

