  position: fixed;
  z-index: 100;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  width: 100%;
  max-width: 400px;
  background-color: #383838;
  padding: 20px;
  color: #fff;
  box-shadow: rgba(0, 0, 0, 0.1);
  border-radius: 5px;
}
