
<template>
  <div class="container">
    
    <form @submit.prevent="addItineraire" class="row form-form form-75">

      <div class="title">
        <h4>
          <span>Ajout</span> <span>Itineraire</span>
        </h4>
        <div class="line">
        
        </div>
      </div>

      <h5>Informations  Itineraire</h5>
      <div class="col-12 col-md-12">
          <b-form-group label="Ligne :">
            <b-form-select @change="handleSelectedLigneChange" v-model="new_itineraire.ligne" :options="lignes" required></b-form-select>
          </b-form-group>
      </div>
      
      <div class="col-12">
        <div class="table-responsive">
          <table class="table">
            <thead>
              <tr>
                <th>Numéro d'immatriculation</th>
                <th>Action</th>
           
              </tr>
            </thead>
            <tbody>
              
                <tr class="" v-for="bus in buss" :key="bus.pk">
                  <td>
                    {{bus.repr}}
                  </td>
                  <td>
                    <input type="checkbox" name="" @input="handleChangeBusCheckBox(bus.pk)">
                    
                  </td>
                 
                </tr>
       
            </tbody>
          </table>
        </div>
       
      </div>
      <h5>Horaire De l'Itineraire</h5>
      <div class="col-12">
        <label for="">Inverser Lieu Ligne</label>
        <input type="checkbox" name="" @change="handleLigneInverseChange" v-model="new_itineraire.ligne_inverse" id="">
    </div>
      <div class="col-12 ">
           
        <div class="table-responsive">

          <table class="table">
                <thead>
                  <tr>
                      <th>Lieu</th>
                      <th>Heure Début</th>
                      <th>Heure Fin</th>
                    </tr>
                </thead>
                <tbody>
                <template v-if="new_itineraire.selectedLigne">
                    <tr  v-for="(ordrelieu,idx) in new_itineraire.selectedLigne.ordrelieus" :key="idx">
                      <td><input v-model="new_itineraire.horaires[idx].pointArret" hidden required>{{ordrelieu.lieu_ligne.repr}}</td>
                      <td><input v-model="new_itineraire.horaires[idx].heureDebut" required type="time"></td>
                      <td><input v-model="new_itineraire.horaires[idx].heureFin" required type="time"></td>
                    </tr>
                  </template>
                  <template v-else>
                    <tr>
                      <td colspan="3">La variable 'ligne' n'est pas définie.</td>
                    </tr>
                  </template>
                </tbody>
              </table>
            </div>    
       </div>
    

      <b-button type="submit" variant="" class="btn btn-save">
        <font-awesome-icon :icon="['fas', 'floppy-disk']" />
        <span>Enregistrer</span> 
      </b-button>
    
    </form>
  </div>
</template>

<script>
import axios from 'axios';


export default {
  name: 'ItineraireCreatePage',

  mounted() {
      this.fecthDataLigne(this.baseUrlLigne);
      this.fecthDataBus(this.baseUrlBus);
      
    },
 data() {
    return {

      baseUrlPage:`${this.$store.state.baseUrl}/api/transport_scolaire/itineraires/`,
      baseUrlLigne:`${this.$store.state.baseUrl}/api/transport_scolaire/lignes/`,
      baseUrlBus:`${this.$store.state.baseUrl}/api/transport_scolaire/buss/`,
      lignes:[],
      buss:[],
      new_itineraire: {
          nom: '',
          horaires:[],
          ligne:'',
          ligne_inverse:false,
          selectedLigne:'',
          busassignations:[],
      },
      
     };
  },
  methods: {
    handleSelectLieuClickDelete(num_ordre,event){
      event
      const index = this.new_itineraire.ordrelieus.findIndex(a => a.num_ordre === num_ordre);
      if (index !== -1) {
        this.new_itineraire.ordrelieus.splice(index, 1);
      }
    },
    handleChangeBusCheckBox(bus){
          let index=this.new_itineraire.busassignations.findIndex((el)=>el.bus==bus)
          if(index==-1){  
               this.new_itineraire.busassignations.push({
                   bus:bus,
               })
          }else{
              this.new_itineraire.busassignations.splice(index,1)
          }
         
    },
    handleLigneInverseChange(){
         if(this.new_itineraire.selectedLigne){
           this.new_itineraire.selectedLigne.ordrelieus=this.new_itineraire.selectedLigne.ordrelieus.reverse();
           this.new_itineraire.horaires=this.new_itineraire.horaires.reverse()
         }
           
    },
    handleSelectedLigneChange(event) {
    
      const selectItem=this.lignes.find(pr=>pr.pk==event)
      const n=selectItem.ordrelieus.length
    
      this.new_itineraire.horaires = Array.from({ length: n }, (_, i) => (
        {
           idx: i,
           pointArret:selectItem.ordrelieus[i].lieu_ligne.pk, 
        }));
      console.log(this.new_itineraire.horaires)
      this.new_itineraire.selectedLigne=selectItem

      
    },

    fecthDataLigne(page){
              // Effectuer la requête GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: false, // Valeur de recherche
                    special_detail_manytoone_class:"['ordrelieus',]"
              } 
              })
                .then(response => {
                   const data = response.data;
                   this.lignes = data.map(a => ({...a, value: a.pk, text: a.repr}));
                   console.log(this.lignes)
                })
                .catch(error => {
                     console.error("Une erreur s'est produite lors de la récupération des lignes :" , error);
                });
      },
      fecthDataBus(page){
              // Effectuer la requête GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: false, // Valeur de recherche
                    
              } 
              })
                .then(response => {
                   const data = response.data;
                   this.buss = data.map(a => ({...a, value: a.pk, text: a.repr}));
                   console.log(this.buss)
                })
                .catch(error => {
                     console.error("Une erreur s'est produite lors de la récupération des buss :" , error);
                });
      },

      addOrderLieu() {
        let orde = 1;
        if (this.new_itineraire.ordrelieus.length > 0) {
          orde = this.new_itineraire.ordrelieus[this.new_itineraire.ordrelieus.length - 1].num_ordre + 1;
        }

        console.log(orde);
        const newOrderLieu = {
          num_ordre: orde,
          lieu_itineraire: ''
        };
        this.new_itineraire.ordrelieus.push(newOrderLieu);
        console.log(this.new_itineraire.ordrelieus);
      },
    addItineraire() {
        console.log(this.new_itineraire)

          // Créez un nouvel objet Project avec les valeurs du formulaire
          // Envoyer la requête POST à l'API avec les données du projet
         axios.post(this.baseUrlPage, this.new_itineraire)
              .then(response => {
              // Traitez la réponse de l'API en conséquence
              console.log(response.data);
                    this.$router.push('/transport_scolaire/itineraire');
              })
              .catch(error => {
              // Gérez les erreurs de la requête
                 console.error(error);
              });
     }

  },




}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.lieu{


  border: 1px solid rgb(43, 2, 21);
  box-shadow: rgba(0, 0, 0, 0.1);
  border-radius: 5px;
}
.lieu .action{
  color:  red;
  display: flex;
  align-items: center;
  font-size: 22px;
}

</style>
