
<template>
    <div class="container">
      
      <form @submit.prevent="addLieuLigne" class="row form-form form-75">

        <div class="title">
          <h4>
            <span>Ajout</span> <span>LieuLigne</span>
          </h4>
          <div class="line">
          
          </div>
        </div>

        <h5>Informations  LieuLigne</h5>
        <div class="col-12   col-md-12">
          <b-form-group label="Nom lieu :">
            <b-form-input id="nom" v-model="new_lieuligne.nom_lieu" required></b-form-input>
          </b-form-group>
        </div>
        <div class="col-12 col-md-6">
          <b-form-group label="Longitude :">
            <b-form-input id="prenoms" v-model="new_lieuligne.longitude" required></b-form-input>
          </b-form-group>
        </div>
      
        <div class="col-12 col-md-6">
          <b-form-group label="Latitude :">
            <b-form-input v-model="new_lieuligne.latitude" required></b-form-input>
          </b-form-group>
        </div>
        <div  class="col-12 autre">
           <div class="row autre">
              <div class="col-10">
                  <b-form-input  v-model="search_query" required></b-form-input>
                  <ul v-if="suggestions.length">
                    <li @click="updateSearch(suggestion.display_name)" v-for="suggestion in suggestions" :key="suggestion.place_id">
                      {{ suggestion.display_name }}
                    </li>
                  </ul>
               </div>
               <div class="col-2">
                <b-button   @click="handleSearch($event)" class="btn autre btn-secondary ">Reachercher</b-button>
               </div>
              
          </div>
        </div>
        <div style="height:600px; width:800px">
          <l-map ref="map" v-on:click="addMarker" @dblclick="handleMapDoubleClick" v-model:zoom="zoom" :center="center">
            <l-tile-layer v-if="satelliteMode"
              url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
              layer-type="base"
              name="OpenStreetMap"
            ></l-tile-layer>
            <l-tile-layer
            v-if="!satelliteMode"
              ref="satelliteTileLayer"
              url="https://mt0.google.com/vt/lyrs=s,h&x={x}&y={y}&z={z}"
              maxZoom="20"
              subdomains="mt0,mt1,mt2,mt3"
            >
          </l-tile-layer>
            <l-marker ref="marquer" :lat-lng="marquer" :icon="icon"  :draggable=true @dragend="handleMarkerDragEnd">
   
            </l-marker>
          </l-map>
      </div>
       
      <b-button type="submit" variant="" class="btn btn-save">
        <font-awesome-icon :icon="['fas', 'floppy-disk']" />
        <span>Enregistrer</span> 
      </b-button>
      
      </form>
    </div>
  </template>
  
  <script>
  import axios from 'axios';

  import "leaflet/dist/leaflet.css";

  import L from 'leaflet';
import { LMap, LTileLayer,LMarker } from "@vue-leaflet/vue-leaflet";

 export default {
    name: 'LieuLigneCreatePage',
    components: {
      LMap,
      LTileLayer,
      LMarker,
   
    },
    mounted() {
        //   this.maFonction("created");
    },
   

    watch: {
      'search_query': function(newValue, oldValue) {
        oldValue
          //  this.searchPlace(newValue)
           if (newValue.length > 0) {
        this.fetchSuggestions(newValue);
      } else {
        this.suggestions = [];
      }
      },
    },

   
   data() {
      return {
         zoom: 8,
         center:[-18.87919, 47.50791],
         satelliteMode: false,
         search_query:"",
         suggestions: [],
         icon: L.icon({
        iconUrl: `${this.$store.state.baseUrl}/static/marker.png`,
        iconSize: [90, 90],
        iconAnchor: [16, 37]
          }),

        baseUrlPage:`${this.$store.state.baseUrl}/api/transport_scolaire/lieulignes/`,
        marquer: L.latLng(-18.85565626, 47.5066423416137),
          new_lieuligne: {
            nom_lieu: '',
            longitude: '47.50664234161377',
            latitude: '-18.85565626',
            lat:-18.85565626,
            lng:47.50664234161377
        },

       };
    },
    methods: {
      updateSearch(searchValue){
          this.search_query=searchValue
          this.searchPlace(this.search_query)
          this.suggestions=[]
      },
      fetchSuggestions(searchValue) {


          searchValue = searchValue.toLowerCase(); // Convertir en minuscules

        if (!searchValue.includes('antananarivo')) {
          searchValue = 'antananarivo ' + searchValue;
        }
        var xhr = new XMLHttpRequest();
  xhr.open('GET', 'https://nominatim.openstreetmap.org/search?q=' + encodeURIComponent(searchValue) + '&format=json&limit=5', true);
  xhr.onload = () => {
    if (xhr.status === 200) {
      var response = JSON.parse(xhr.responseText);
      if (response.length > 0) {
        this.suggestions=response
      } else {
        this.suggestions=[]
      }
    } else {
      console.log('Erreur de requête :', xhr.status);
      this.suggestions=[]
    }
  };
  xhr.send();
    },
      handleSearch(event){
          event.preventDefault()
          this.searchPlace(this.search_query)
      },
      handleMapDoubleClick(event) {
        const position = event.latlng;
        console.log('Position du double-clic :', position);
        this.new_lieuligne.latitude= this.new_lieuligne.lat=position.lat;
        this.new_lieuligne.longitude= this.new_lieuligne.lng=position.lng;



       this.marquer=position
       this.new_lieuligne.latitude=String(position.lat);
        this.new_lieuligne.longitude=String(position.lng)
        
       

      },
      handleMarkerDragEnd(event){
        const draggedMarker = event.target;
        console.log(draggedMarker)
        const position = draggedMarker.getLatLng();
        this.new_lieuligne.latitude=String(position.lat);
        this.new_lieuligne.longitude=String(position.lng)
        // console.log('Nouvelle position :', position);
      },
      toggleSatellite() {
    // Inverser l'état du mode satellite
      this.satelliteMode = !this.satelliteMode;

    },
      addLieuLigne() {

              console.log(this.new_lieuligne)
              // if(this.new_lieuligne)
              //   return 0
            // Créez un nouv el objet Project avec les valeurs du formulaire



            // Envoyer la requête POST à l'API avec les données du projet
            axios.post(this.baseUrlPage, this.new_lieuligne)
                .then(response => {
                // Traitez la réponse de l'API en conséquence
                console.log(response.data);


                this.$router.push('/transport_scolaire/lieuligne');
                })
                .catch(error => {
                // Gérez les erreurs de la requête
                   console.error(error);
                });
      
      
   
       },
       updateMap(lat, lng) {
          this.center = [lat, lng];
          this.zoom=16;        
      },
searchPlace(searchValue) {
  searchValue = searchValue.toLowerCase(); // Convertir en minuscules

if (!searchValue.includes('antananarivo')) {
  searchValue = 'antananarivo ' + searchValue;
}

  var xhr = new XMLHttpRequest();
  xhr.open('GET', 'https://nominatim.openstreetmap.org/search?q=' + encodeURIComponent(searchValue) + '&format=json&limit=1', true);
  xhr.onload = () => {
    if (xhr.status === 200) {
      var response = JSON.parse(xhr.responseText);
      if (response.length > 0) {
        var latitude = parseFloat(response[0].lat);
        var longitude = parseFloat(response[0].lon);

        this.updateMap(latitude, longitude);
      } else {
        this.searchByRoad(searchValue);
      }
    } else {
      console.log('Erreur de requête :', xhr.status);
    }
  };
  xhr.send();
},
searchByRoad(searchValue) {
  var xhr = new XMLHttpRequest();
  xhr.open('GET', 'https://nominatim.openstreetmap.org/route?format=json&limit=1&q=' + encodeURIComponent(searchValue), true);
  xhr.onload = () => {
    if (xhr.status === 200) {
      var response = JSON.parse(xhr.responseText);
      if (response.length > 0) {
        var latitude = parseFloat(response[0].lat);
        var longitude = parseFloat(response[0].lon);

        this.updateMap(latitude, longitude);
      } else {
        console.log('Aucun résultat trouvé pour la recherche par route');
      }
    } else {
      console.log('Erreur de requête :', xhr.status);
    }
  };
  xhr.send();
}
    },




  }
</script>

  <!-- Add "scoped" attribute to limit CSS to this component only -->
  <style scoped>
.popup-task{
    position: fixed;
    z-index: 100;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 100%;
    max-width: 400px;
    background-color: #383838;
    padding: 20px;
    color: #fff;
    box-shadow: rgba(0, 0, 0, 0.1);
    border-radius: 5px;
}
 .autre{
  padding-left:0 !important;
 }
  </style>
  
