import axios from "axios";

const apiService = axios.create();

// Configurer les en-têtes par défaut si le jeton d'accès est présent
if (localStorage.getItem('access')) {
    apiService.defaults.headers.common['Authorization'] = `Bearer ${localStorage.getItem('access')}`;
} else {
    apiService.defaults.headers.common['Authorization'] = null;
}

export default apiService;
