<template>
    <div>
      
      
      
      <div class="cont-sep">
        <div class="title">
          <h4>
               <span>Liste</span> <span>Chauffeurs</span>
          </h4>
          <div class="line">
           
          </div>
        </div>
        <div class="actions">
          <router-link to="/transport_scolaire/chauffeur/nouveau" class="btn btn-add">
            <font-awesome-icon :icon="['fas', 'plus']" /> Ajouter un nouveau chauffeur
          </router-link>
  
        </div>
       
        <div class="search-form">
           <input type="text" v-model="searchQuery" placeholder="Rechercher  chauffeur ..." />
           <font-awesome-icon :icon="['fas', 'magnifying-glass']" class="icon"/>
        </div>
        <div class="table-responsive">
        <table class="table">
            <thead>
              <tr>
                <th>N°</th>
                <th>Nom</th>
                <th>Prénoms</th>
                <th>Email</th>
                <th>Téléphone</th>
                <th>Actions</th>
              </tr>
            </thead>
            <transition-group name="chauffeur-table" tag="tbody">
              <tr v-for="chauffeur in chauffeurs" :key="chauffeur.pk">
                <td>{{ chauffeur.pk }}</td>
                <td>{{ chauffeur.nom }}</td>
                <td>{{ chauffeur.prenoms }}</td>
                <td>{{ chauffeur.email }}</td>
                <td>{{ chauffeur.tel }}</td>
                <td>
                  
                 <router-link :to="`/transport_scolaire/chauffeur/${$encryptId(chauffeur.pk)}/edit`" class="btn-action"> 
                    <font-awesome-icon :icon="['fas', 'file-pen']" />
                  </router-link>
                  <router-link :to="`/transport_scolaire/chauffeur/${$encryptId(chauffeur.pk)}/delete`" class="btn-action" >
                    <font-awesome-icon :icon="['fas', 'x']" />
                  </router-link> 
                </td>
              </tr>
            </transition-group>
        </table>
      </div>
        <div class="pagination">
              <div class="page">
                 <span>Page</span>  <span>{{current_page}}</span> sur <span>{{ nbr_page }}</span>
              </div>
              <div class="btns">
                        <button @click="goToPageCandidat(previousPageCandidat)" :disabled="!previousPageCandidat" class="btn">
                          <font-awesome-icon :icon="['fas', 'chevron-left']" />
                      </button>
                      <button @click="goToPageCandidat(previousPageCandidat)"  class="btn">
                        {{current_page}}
                      </button>
                      <button @click="goToPageCandidat(nextPageCandidat)" :disabled="!nextPageCandidat" class="btn">
                        <font-awesome-icon :icon="['fas', 'chevron-right']" />
                  </button>
              </div>
              <div class="ads btn"  >
                  .
              </div>
             
           </div>
       <div class="popup-buttons">
            
        </div>
      </div>
      
    </div>
  </template>
  
<script>

import axios from 'axios';

export default {
    name: 'ChauffeurPage',
    data() {
      return {
        baseUrlPage:`${this.$store.state.baseUrl}/api/transport_scolaire/chauffeurs/`,
        chauffeurs: [],
        searchQuery: '',
        startDate: '',
        endDate: '',
        current_page:2,
        nbr_page:10,
      };
    },
    computed: {
      
    },
    methods: {
      fecthData(page){
              // Effectuer la requête GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: true, // Valeur de recherche
                } 
                })
                .then(response => {
                   const data = response.data;
                   this.chauffeurs = data.results
                })
                .catch(error => {
                     console.error("Une erreur s'est candidate lors de la récupération des chauffeurs:", error);
                });
          },
    },
    mounted() {
       this.fecthData(this.baseUrlPage)
    }
  };
  </script>
  
  <style scoped>
  
  .chauffeur-table-enter {
    transition: transform 1s;
  }
  </style>