          </b-form-group>
        </div>
        <div class="col-12 col-md-6">
          <b-form-group label="Prénom :">
            <b-form-input id="prenoms" v-model="new_eleve.prenoms" required></b-form-input>
          </b-form-group>
        </div>
      
        <div class="col-12">
          <b-form-group label="Date Naissance :">
            <b-form-input id="date_naissance" v-model="new_eleve.agge" type="date" required></b-form-input>
          </b-form-group>
        </div>
        <div class="col-12 col-md-6">
          <b-form-group label="Ecole :">
            <b-form-select v-model="new_eleve.ecole" :options="ecoles" required></b-form-select>
