          </b-form-group>
        </div>
        <h5>Informations Parent Elève</h5>
      <div class="col-12 col-md-6">
          <b-form-group label="Nom Père :">
            <b-form-input v-model="new_eleve.nom_pere" required></b-form-input>
          </b-form-group>
      </div>
      <div class="col-12 col-md-6">
      
          <b-form-group label="Tel :">
            <b-form-input v-model="new_eleve.tel_pere" required></b-form-input>
          </b-form-group>
      </div>
      <div class="col-12 col-md-6">
          <b-form-group label="Nom Mère :">
            <b-form-input v-model="new_eleve.nom_pere" required></b-form-input>
          </b-form-group>
      </div>
      <div class="col-12 col-md-6">
        <b-form-group label="Tel :">
          <b-form-input v-model="new_eleve.tel_pere" required></b-form-input>
        </b-form-group>
      </div>
     
    
        <b-button type="submit" variant="" class="btn btn-save">
          <font-awesome-icon :icon="['fas', 'floppy-disk']" />
          <span>Enregistrer</span> 
        </b-button>
      
      </form>
    </div>
  </template>
  
  <script>
  import axios from 'axios';
  const baseUrl="localhost:8000"
  
 export default {
    name: 'EleveCreatePage',
    mounted() {
        //   this.maFonction("created");
    },

   data() {
      return {

  
          new_eleve: {
            nom: '',
            prenoms: '',
            date_naissance: '',
            ecole: '',
            niveau: '',
            classe: '',
            ligne_bus: '',
            lieu_ligne: '',
            lieu_ramassage: ''
        },
        ecoles: [
            { value: 1, text: 'Ecole 1' },
            { value: 2, text: 'Ecole 2' },
            { value: 3, text: 'Ecole 3' }
         ],
         classes: [
            { value: 1, text: 'Classe 1' },
            { value: 2, text: 'Classe 2' },
            { value: 3, text: 'Classe 3' }
          ],
           
       };
    },
    methods: {
       
      addEleve() {


            // Créez un nouvel objet Project avec les valeurs du formulaire
            const new_eleve = {
nom: this.new_eleve.nom,
agge: this.new_eleve.agge,
};


            // Envoyer la requête POST à l'API avec les données du projet
            axios.post(`http://${baseUrl}/api/eleve`, new_eleve)
                .then(response => {
                // Traitez la réponse de l'API en conséquence
                console.log(response.data);
                     // Réinitialisez les valeurs du formulaire
          this.new_eleve.nom = null ;
          this.new_eleve.agge = null ;

                this.$router.push('/Eleve');
                })
                .catch(error => {
                // Gérez les erreurs de la requête
                console.error(error);
                });
      
      
   
       }
  
    },




  }
</script>

