<template>
    <div>
      
      
      
      <div class="cont-sep">
        <div class="title">
          <h4>
               <span>Liste</span> <span>LieuRamassages</span>
          </h4>
          <div class="line">
           
          </div>
        </div>
        <div class="actions">
          <router-link to="/transport_scolaire/lieuramassage/nouveau" class="btn btn-add">
            <font-awesome-icon :icon="['fas', 'plus']" /> Ajouter un nouveau lieuramassage
          </router-link>
  
        </div>
       
        <div class="search-form">
           <input type="text" v-model="searchQuery" placeholder="Rechercher  lieuramassage ..." />
           <font-awesome-icon :icon="['fas', 'magnifying-glass']" class="icon"/>
        </div>
        <div class="table-responsive">
        <table class="table">
            <thead>
              <tr>
                <th>N°</th>
                <th>Nom</th>
                <th>Lieu Ligne</th>
                <th>longitude</th>
                <th>latitude</th>
                <th>Actions</th>
              </tr>
            </thead>
            <transition-group name="lieuramassage-table" tag="tbody">
              <tr v-for="lieuramassage in lieuramassages" :key="lieuramassage.pk">
                <td>{{ lieuramassage.pk }}</td>
                <td>{{ lieuramassage.nom_lieu }}</td>
                <td>{{ lieuramassage.lieu_ligne }}</td>
                <td>{{ lieuramassage.longitude }}</td>
                <td>{{ lieuramassage.latitude }}</td>
                <td>
                  
                 <router-link :to="`/transport_scolaire/lieuramassage/${$encryptId(lieuramassage.pk)}/edit`" class="btn-action"> 
                    <font-awesome-icon :icon="['fas', 'file-pen']" />
                  </router-link>
                  <router-link :to="`/transport_scolaire/lieuramassage/${$encryptId(lieuramassage.pk)}/delete`" class="btn-action" >
                    <font-awesome-icon :icon="['fas', 'x']" />
                  </router-link> 
                </td>
              </tr>
            </transition-group>
        </table>
      </div>
        <div class="pagination">
              <div class="page">
                 <span>Page</span>  <span>{{current_page}}</span> sur <span>{{ nbr_page }}</span>
              </div>
              <div class="btns">
                        <button @click="goToPageCandidat(previousPageCandidat)" :disabled="!previousPageCandidat" class="btn">
                          <font-awesome-icon :icon="['fas', 'chevron-left']" />
                      </button>
                      <button @click="goToPageCandidat(previousPageCandidat)"  class="btn">
                        {{current_page}}
                      </button>
                      <button @click="goToPageCandidat(nextPageCandidat)" :disabled="!nextPageCandidat" class="btn">
                        <font-awesome-icon :icon="['fas', 'chevron-right']" />
                  </button>
              </div>
              <div class="ads btn"  >
                  .
              </div>
             
           </div>
       <div class="popup-buttons">
            
        </div>
      </div>
      
    </div>
  </template>
  
<script>

import axios from 'axios';

export default {
    name: 'LieuRamassagePage',
    data() {
      return {
        baseUrlPage:`${this.$store.state.baseUrl}/api/transport_scolaire/lieuramassages/`,
        lieuramassages: [],
        searchQuery: '',
        startDate: '',
        endDate: '',
        current_page:2,
        nbr_page:10,
      };
    },
    computed: {
      
    },
    methods: {
      fecthData(page){
              // Effectuer la requête GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: true, // Valeur de recherche
                } 
                })
                .then(response => {
                   const data = response.data;
                   this.lieuramassages = data.results
                   console.log(data.results)
                })
                .catch(error => {
                     console.error("Une erreur s'est candidate lors de la récupération des lieuramassages:", error);
                });
          },
    },
    mounted() {
       this.fecthData(this.baseUrlPage)
    }
  };
  </script>
  
  <style scoped>
  
  .lieuramassage-table-enter {
    transition: transform 1s;
  }
  </style>