<template>
  <div>
    
    
    
    <div class="cont-sep">
      <div class="title">
        <h4>
             <span>Liste</span> <span>Parents</span>
        </h4>
        <div class="line">
         
        </div>
      </div>
      <div class="actions">
        <router-link to="/transport_scolaire/parent/nouveau" class="btn btn-add">
          <font-awesome-icon :icon="['fas', 'plus']" /> Ajouter un nouveau élève
        </router-link>

      </div>
      <div class="filter-form">
          <div class="item-group">
              <div class="item">
                <label>Date de début:</label>
                <input type="date" v-model="startDate" />
            </div>
              <div class="item">
                <label>Date de fin:</label>
                <input type="date" v-model="endDate" />
              </div>
              <div class="item">
                <label>Ligne:</label>
                <select name="" id="">
                  <option value="">-------------------</option>
                </select>
              </div>
          </div>
          <font-awesome-icon :icon="['fas', 'filter']" class="icon"/>

      </div>
      <div class="search-form">
         <input type="text" v-model="searchQuery" placeholder="Rechercher  élève ..." />
         <font-awesome-icon :icon="['fas', 'magnifying-glass']" class="icon"/>
      </div>
      <div class="table-responsive">
      <table class="table">
          <thead>
            <tr>
              <th>N°</th>
              <th>Nom Mère</th>
              <th>Tel</th>
              <th>Nom Père</th>
              <th>Tel</th>
              <th>Email</th>
              <th>Actions</th>
            </tr>
          </thead>
          <transition-group name="parent-table" tag="tbody">
            <tr v-for="parent in parents" :key="parent.pk">
              <td>{{ parent.pk }}</td>
              <td>{{ parent.nom_mere }}</td>
              <td>{{ parent.tel_mere }}</td>
              <td>{{ parent.nom_pere }}</td>
              <td>{{ parent.tel_pere }}</td>
              <td>{{ parent.email }}</td>
              <td>
                
                <router-link :to="`/transport_scolaire/parent/${$encryptId(parent.pk)}/edit`" class="btn-action">
                                    <font-awesome-icon :icon="['fas', 'file-pen']" />
                </router-link>
                <router-link :to="`/transport_scolaire/parent/${$encryptId(parent.pk)}/delete`" class="btn-action" >
                  <font-awesome-icon :icon="['fas', 'x']" />
                </router-link> 
              </td>
            </tr>
          </transition-group>
      </table>
    </div>
      <div class="pagination">
            <div class="page">
               <span>Page</span>  <span>{{current_page}}</span> sur <span>{{ nbr_page }}</span>
            </div>
            <div class="btns">
                      <button @click="goToPageCandidat(previousPageCandidat)" :disabled="!previousPageCandidat" class="btn">
                        <font-awesome-icon :icon="['fas', 'chevron-left']" />
                    </button>
                    <button @click="goToPageCandidat(previousPageCandidat)"  class="btn">
                      {{current_page}}
                    </button>
                    <button @click="goToPageCandidat(nextPageCandidat)" :disabled="!nextPageCandidat" class="btn">
                      <font-awesome-icon :icon="['fas', 'chevron-right']" />
                </button>
            </div>
            <div class="ads btn"  >
                .
            </div>
           
         </div>
     <div class="popup-buttons">
          
      </div>
    </div>
    
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'parentPage',
  props: {
        baseUrl: String
      },
  data() {
    return {
      baseUrlPage:`${this.$store.state.baseUrl}/api/transport_scolaire/parents/`,

      parents: [],
     
      searchQuery: '',
      startDate: '',
      endDate: '',
      current_page:2,
      nbr_page:10,
    };
  },
  computed: {
   
    },

  methods: {

    fecthData(page){
              // Effectuer la requête GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: true, // Valeur de recherche
                } 
                })
                .then(response => {
                   const data = response.data;
                   this.parents = data.results
                })
                .catch(error => {
                     console.error("Une erreur s'est produit lors de la récupération des élèves:", error);
                });
          },
  },

  mounted() {
    this.fecthData(this.baseUrlPage)
    }
};
</script>

<style scoped>

.parent-table-enter {
  transition: transform 1s;
}
</style>