<template>
    <div>
      
      
      
      <div class="cont-sep">
        <div class="title">
          <h4>
               <span>Liste</span> <span>Bus</span>
          </h4>
          <div class="line">
           
          </div>
        </div>
        <div class="actions">
          <router-link to="/transport_scolaire/bus/nouveau" class="btn btn-add">
            <font-awesome-icon :icon="['fas', 'plus']" /> Ajouter un nouveau bus
          </router-link>
  
        </div>
        <div class="filter-form">
            <div class="item-group">
                <div class="item">
                  <label>Date de début:</label>
                  <input type="date" v-model="startDate" />
              </div>
                <div class="item">
                  <label>Date de fin:</label>
                  <input type="date" v-model="endDate" />
                </div>
                <div class="item">
                  <label>Ligne:</label>
                  <select name="" id="">
                    <option value="">-------------------</option>
                  </select>
                </div>
            </div>
            <font-awesome-icon :icon="['fas', 'filter']" class="icon"/>
  
        </div>
        <div class="search-form">
           <input type="text" v-model="searchQuery" placeholder="Rechercher  élève ..." />
           <font-awesome-icon :icon="['fas', 'magnifying-glass']" class="icon"/>
        </div>
        <div class="table-responsive">
        <table class="table">
            <thead>
              <tr>
                <th>N°</th>
                <th>Immatriculation</th>
                <th>Nombre Place</th>
                <th>Couleur</th>
                <th>Type</th>
                <th>Chauffeur</th>
             
                <th>Actions</th>
              </tr>
            </thead>
            <transition-group name="bus-table" tag="tbody">
              <tr v-for="bus in buss" :key="bus.id">
                <td>{{ bus.pk }}</td>
                <td>{{ bus.immatriculation }}</td>
                <td>{{ bus.nbr_place }}</td>
                <td>
           
                <span class="couleur-bus" :style="{
                        background: bus.couleur,
                        color:'#fff',
                        display:'inline-block', 
                        width: '100%',
                        height: '100%' ,
                        textAlign:'center',
                      }
                ">{{bus.couleur}}</span>

                </td>
                <td>{{ bus.type }}</td>
                <td>{{ bus.chauffeur }}</td>
            
                <td>
                  
                 <router-link :to="`/transport_scolaire/bus/${$encryptId(bus.pk)}/edit`" class="btn-action"> 
                    <font-awesome-icon :icon="['fas', 'file-pen']" />
                  </router-link>
                  <router-link :to="`/transport_scolaire/bus/${$encryptId(bus.pk)}/delete`" class="btn-action" >
                    <font-awesome-icon :icon="['fas', 'x']" />
                  </router-link> 
                </td>
              </tr>
            </transition-group>
        </table>
      </div>
        <div class="pagination">
              <div class="page">
                 <span>Page</span>  <span>{{current_page}}</span> sur <span>{{ nbr_page }}</span>
              </div>
              <div class="btns">
                        <button @click="goToPageCandidat(previousPageCandidat)" :disabled="!previousPageCandidat" class="btn">
                          <font-awesome-icon :icon="['fas', 'chevron-left']" />
                      </button>
                      <button @click="goToPageCandidat(previousPageCandidat)"  class="btn">
                        {{current_page}}
                      </button>
                      <button @click="goToPageCandidat(nextPageCandidat)" :disabled="!nextPageCandidat" class="btn">
                        <font-awesome-icon :icon="['fas', 'chevron-right']" />
                  </button>
              </div>
              <div class="ads btn">
                  .
              </div>
             
           </div>
       <div class="popup-buttons">
            
        </div>
      </div>
      
    </div>
  </template>
  
  <script>

import axios from 'axios';

  export default {
    name: 'BusPage',
    data() {
      return {
        buss: [],
        baseUrlPage:`${this.$store.state.baseUrl}/api/transport_scolaire/buss/`,
        searchQuery: '',
        startDate: '',
        endDate: '',
        current_page:2,
        nbr_page:10,
      };
    },
    computed: {
    },
    methods: {
        fecthData(page){
                // Effectuer la requête GET vers l'API en utilisant Axios
                axios.get(page,{ params: {
                      paginate: true, // Valeur de recherche
                  } 
                  })
                  .then(response => {
                    const data = response.data;
                    this.buss = data.results
                  })
                  .catch(error => {
                      console.error("Une erreur s'est candidate lors de la récupération des chauffeurs:", error);
                  });
            },
      },
    mounted() {
       this.fecthData(this.baseUrlPage)
    }
  };
  </script>
  
  <style scoped>
  
  .bus-table-enter {
    transition: transform 1s;
  }
  </style>