<template>
  <div>
    
    
    
    <div class="cont-sep">
      <div class="title">
        <h4>
             <span>Liste</span> <span>Itineraire</span>
        </h4>
        <div class="line">
         
        </div>
      </div>
      <div class="actions">
        <router-link to="/transport_scolaire/itineraire/nouveau" class="btn btn-add">
          <font-awesome-icon :icon="['fas', 'plus']" /> Ajouter un nouveau itineraire
        </router-link>

      </div>
      <div class="filter-form">
          <div class="item-group">
            <div class="item">
              <label>Bus:</label>
              <select name="" id="">
                <option value="">-------------------</option>
              </select>
            </div>
              <div class="item">
                <label>Ligne:</label>
                <select name="" id="">
                  <option value="">-------------------</option>
                </select>
              </div>
          </div>
          <font-awesome-icon :icon="['fas', 'filter']" class="icon"/>

      </div>
      <div class="search-form">
         <input type="text" v-model="searchQuery" placeholder="Rechercher  élève ..." />
         <font-awesome-icon :icon="['fas', 'magnifying-glass']" class="icon"/>
      </div>
      <div class="table-responsive">
      <table class="table">
          <thead>
            <tr>
              <th>N°</th>
              <th>Ligne</th>
              <th>Lieu Départ</th>
              <th>Lieu Arrivée</th>
              <th>Heure Départ</th>
              <th>Heure d'arrivié</th>
              <th>Bus</th>
              <th>Actions</th>
            </tr>
          </thead>
          <transition-group name="itineraire-table" tag="tbody">
            <tr v-for="itineraire in itineraires" :key="itineraire.id">
              <td>{{ itineraire.pk }}</td>
              <td>{{ itineraire.ligne }}</td>
              <td v-if="itineraire.lieuDepart">{{ itineraire.lieuDepart.repr }}</td>
              <td v-if="!itineraire.lieuDepart">------------</td>
              <td v-if="itineraire.lieuArrivee">{{ itineraire.lieuArrivee.repr }}</td>
              <td v-if="!itineraire.lieuArrivee">-------------</td>
              <td>{{ itineraire.heureDepart }}<template v-if="!itineraire.heureDepart">-------------</template></td>
              <td>{{ itineraire.heureArrivee }}<template v-if="!itineraire.heureArrivee">-------------</template></td>
              <td>
                      <div v-for="(busassignation,idx) in itineraire.busassignations" :key="idx">
                          {{busassignation.bus.repr}} 
                      </div>
                  <template v-if="itineraire.busassignations.length === 0">-----------</template>
              </td>
             
          
              <td>
                
               <router-link :to="`/transport_scolaire/itineraire/${$encryptId(itineraire.pk)}/edit`" class="btn-action"> 
                  <font-awesome-icon :icon="['fas', 'file-pen']" />
                </router-link>
                <router-link :to="`/transport_scolaire/itineraire/${$encryptId(itineraire.pk)}/delete`" class="btn-action" >
                  <font-awesome-icon :icon="['fas', 'x']" />
                </router-link> 
              </td>
            </tr>
          </transition-group>
      </table>
    </div>
      <div class="pagination">
            <div class="page">
               <span>Page</span>  <span>{{current_page}}</span> sur <span>{{ nbr_page }}</span>
            </div>
            <div class="btns">
                      <button @click="goToPageCandidat(previousPageCandidat)" :disabled="!previousPageCandidat" class="btn">
                        <font-awesome-icon :icon="['fas', 'chevron-left']" />
                    </button>
                    <button @click="goToPageCandidat(previousPageCandidat)"  class="btn">
                      {{current_page}}
                    </button>
                    <button @click="goToPageCandidat(nextPageCandidat)" :disabled="!nextPageCandidat" class="btn">
                      <font-awesome-icon :icon="['fas', 'chevron-right']" />
                </button>
            </div>
            <div class="ads btn">
                .
            </div>
           
         </div>
     <div class="popup-buttons">
          
      </div>
    </div>
    
  </div>
</template>

<script>

import axios from 'axios';

export default {
  name: 'ItinerairePage',
  data() {
    return {
      itineraires: [],
      baseUrlPage:`${this.$store.state.baseUrl}/api/transport_scolaire/itineraires/`,
      searchQuery: '',
      startDate: '',
      endDate: '',
      current_page:2,
      nbr_page:10,
    };
  },
  computed: {
  },
  methods: {
      fecthData(page){
              // Effectuer la requête GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: true, // Valeur de recherche
                    special_detail_manytoone_class:"['busassignations','horaires']"
                } 
                })
                .then(response => {
                  const data = response.data;
                  this.itineraires = data.results.map((itineraire)=>{
                    
                    if(itineraire.horaires.length>0){
                          itineraire.lieuDepart=itineraire.horaires[0].pointArret	
                          itineraire.heureDepart=itineraire.horaires[0].heureDebut
                          itineraire.lieuArrivee=itineraire.horaires[itineraire.horaires.length-1].pointArret	
                          itineraire.heureArrivee=itineraire.horaires[itineraire.horaires.length-1].heureFin	
                    }else{
                        itineraire.lieuDepart=null
                        itineraire.heureDepart=null
                        itineraire.lieuArrivee=null
                        itineraire.heureArrivee=null
                    }
                   
                    
                   
                    return itineraire
                  })
                  console.log(this.itineraires)
                })
                .catch(error => {
                    console.error("Une erreur s'est produits lors de la récupération des itineraires:", error);
                });
          },
    },
  mounted() {
     this.fecthData(this.baseUrlPage)
  }
};
</script>

<style scoped>

.itineraire-table-enter {
  transition: transform 1s;
}
</style>