
<template>
    <div class="container">
      
      <form @submit.prevent="editLieuRamassage" class="row form-form form-75">
  
        <div class="title">
          <h4>
            <span>Modification</span> <span>LieuRamassage</span>
          </h4>
          <div class="line">
          
          </div>
        </div>
  
        <h5>Informations  LieuRamassage</h5>
         <div class="col-12   col-md-12">
          <b-form-group label="Nom lieu :">
            <b-form-input id="nom" v-model="edit_lieuramassage.nom_lieu" required></b-form-input>
          </b-form-group>
        </div>
        <div class="col-12 col-md-12">
          <b-form-group label="LieuRamassage :">
            <b-form-select v-model="edit_lieuramassage.lieu_ligne" :options="lieu_lignes" ></b-form-select>
          </b-form-group>
        </div>
        <div class="col-12 col-md-6">
          <b-form-group label="Longitude :">
            <b-form-input id="prenoms" v-model="edit_lieuramassage.longitude" required></b-form-input>
          </b-form-group>
        </div>
      
        <div class="col-12 col-md-6">
          <b-form-group label="Latitude :">
            <b-form-input v-model="edit_lieuramassage.latitude" required></b-form-input>
          </b-form-group>
        </div>
    
        <b-button type="submit" variant="" class="btn btn-save">
          <font-awesome-icon :icon="['fas', 'floppy-disk']" />
          <span>Enregistrer les modifications</span> 
        </b-button>
      
      </form>
    </div>
  </template>
  
  <script>
  import axios from 'axios';
 
  
  export default {
    name: 'LieuRamassageCreatePage',
    mounted() {
        const encryptedId = this.$route.params.id; // Récupère l'ID crypté depuis l'URL
        const decryptedId = this.$decryptId(encryptedId); // Décrypte l'ID
        console.log(decryptedId);
         this.fecthDataLieuRamassage(this.baseUrlLieuRamassage);
        this.url=`${this.$store.state.baseUrl}/api/transport_scolaire/lieuramassages/${decryptedId}/`
        // console.log(url)
        this.fecthData(this.url)
    },
  
    data() {
    return {
      url:'',
       baseUrlLieuRamassage:`${this.$store.state.baseUrl}/api/transport_scolaire/lieulignes/`,
      baseUrlPage:`${this.$store.state.baseUrl}/api/transport_scolaire/lieuramassages/`,
      edit_lieuramassage: {
        nom_lieu: '',
        lieu_ligne:"",
        longitude: '-18.881589691375204',
        latitude: '47.50664234161377',
      
      },
       lieu_lignes:[],
    
    };
  },
    methods: {
       fecthDataLieuRamassage(page){
              // Effectuer la requête GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: false, // Valeur de recherche
              } 
              })
                .then(response => {
                   const data = response.data;
                   console.log(data)
                   this.lieu_lignes = data.map(a => ({ value: a.pk, text: a.repr }));
                })
                .catch(error => {
                     console.error("Une erreur s'est candidate lors de la récupération des lieulignes:", error);
                });
      },
      fecthData(page){
              // Effectuer la requête GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: true, // Valeur de recherche
                } 
                })
                .then(response => {
                   const data = response.data;
                   this.edit_lieuramassage = {
                    ...data,
                    lieu_ligne:data.lieu_ligne.pk
                   }
                })
                .catch(error => {
                     console.error("Une erreur s'est candidate lors de la récupération des lieuramassages:", error);
                });
          },
      editLieuRamassage() {
  
  
            // Créez un nouvel objet Project avec les valeurs du formulaire
      
  
  
  
            // Envoyer la requête POST à l'API avec les données du projet
            axios.put(this.url, this.edit_lieuramassage)
                .then(response => {
                // Traitez la réponse de l'API en conséquence
                console.log(response.data);
                     // Réinitialisez les valeurs du formulaire
       
  
                this.$router.push('/transport_scolaire/lieuramassage');
                })
                .catch(error => {
                // Gérez les erreurs de la requête
                console.error(error);
                });
      
      
   
       }
  
    },
  
  
  
  
  }
  </script>
  
  <!-- Add "scoped" attribute to limit CSS to this component only -->
  <style scoped>
  .popup-task{
    position: fixed;
    z-index: 100;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 100%;
    max-width: 400px;
    background-color: #383838;
    padding: 20px;
    color: #fff;
    box-shadow: rgba(0, 0, 0, 0.1);
    border-radius: 5px;
  }
  h3 {
    margin: 40px 0 0;
  }
  ul {
    list-style-type: none;
    padding: 0;
  }
  li {
    display: inline-block;
    margin: 0 10px;
  }
  a {
    color: #42b983;
  }
  </style>
  
  