
<template>
    <div class="container">
      
      <form @submit.prevent="addEleve" class="row form-form form-75">

        <div class="title">
          <h4>
            <span>Ajout</span> <span>Elève</span>
          </h4>
          <div class="line">
          
          </div>
        </div>

        <h5>Informations  Elève</h5>
        <div class="col-12   col-md-6">
          <b-form-group label="nom :">
            <b-form-input id="nom" v-model="new_eleve.nom" required></b-form-input>
          </b-form-group>
          <div class="text-danger">
                {{new_eleve.nom_error}}
          </div>
        </div>
        <div class="col-12 col-md-6">
          <b-form-group label="Prénom :">
            <b-form-input id="prenoms" v-model="new_eleve.prenoms" required></b-form-input>
          </b-form-group>
          <div class="text-danger">
                {{new_eleve.prenoms_error}}
          </div>
        </div>
      
        <div class="col-12">
          <b-form-group label="Date Naissance :">
            <b-form-input id="date_naissance" v-model="new_eleve.date_naissance" type="date" required></b-form-input>
          </b-form-group>
          <div class="text-danger">
                {{new_eleve.date_naissance_error}}
          </div>
        </div>
        <div class="col-12 col-md-6">
          <b-form-group label="Ecole :">
            <b-form-select v-model="new_eleve.ecole" :options="ecoles" required></b-form-select>
          </b-form-group>
          <div class="text-danger">
            {{new_eleve.ecole_error}}
          </div>
        </div>

        <div class="row col-12 col-md-6" v-if="!new_eleve.create_classe">
            <div class="col-10">
               <b-form-group label="Classe :">
               <b-form-select v-model="new_eleve.classe" :options="classes" required></b-form-select>
              </b-form-group>
              <div class="text-danger">
                  {{new_eleve.classe_error}}
              </div>
            </div>
            <div class="col-2">
                <input v-model="new_eleve.create_classe" type="checkbox">
                <label for="">Autre Classe</label>
            </div>
        </div>

        <div v class="row col-12 col-md-6" v-if="new_eleve.create_classe">
          <div class="col-10">
             <b-form-group label="Nouvel classe :">
                 <b-form-input v-model="new_eleve.new_classe.nom" ></b-form-input>
            </b-form-group>
            <div class="text-danger">
                  {{new_eleve.classe_error}}
            </div>
          </div>
          <div class="col-2">
              <input v-model="new_eleve.create_classe" type="checkbox">
              <label for="">Autre Classe</label>
          </div>
      </div>

        <div class="col-12 col-md-6">
            <b-form-group label="Image :">
              <input type="file" accept="image/*" @change="handleImageUploadNewEleve">
            </b-form-group>
            <div class="text-danger">
              {{ new_eleve.image_error }}
            </div>
        </div>
        <div class="col-12">
          <b-form-group label="Adresse :">
            <b-form-input v-model="new_eleve.adresse" required></b-form-input>
          </b-form-group>
          <div class="text-danger">
            {{new_eleve.adresse_error}}
          </div>
        </div>
        <div class="col-12 col-md-6">
          <b-form-group label="Ligne de bus :">
            <b-form-select v-model="new_eleve.ligne" :options="lignes" required></b-form-select>
          </b-form-group>
          <div class="text-danger">
            {{new_eleve.ligne_error}}
          </div>
        </div>
        <div class="col-12 col-md-6">
          <b-form-group label="Lieu de ligne :">
            <b-form-select v-model="new_eleve.lieuligne" :options="lieulignes" required></b-form-select>
          </b-form-group>
            <div class="text-danger">
              {{new_eleve.lieuligne_error}}
            </div>
        </div>
      
        <div class="row" v-if="!new_eleve.create_lieu_ramassage">
            <div class="col-10" >
                <b-form-group label="Lieu de ramassage :">
                  <b-form-select v-model="new_eleve.lieu_ramassage" :options="lieu_ramassages"></b-form-select>
                </b-form-group>
                <div class="text-danger">
                  {{ new_eleve.lieu_ramassage_error }}
                </div>
              </div>
              <div class="col-2 checkbox" >
                   <input type="checkbox" v-model="new_eleve.create_lieu_ramassage" >
                    <label for="">
                      Autre Lieu de ramassage au lieu
                    </label>
                   
                  
              </div>
      </div>
     
      <div class="row" v-if="new_eleve.create_lieu_ramassage">
          <div class="col-11 checkbox">
              <b-form-group label="Nouveau Lieu de Ramassage :">
                <b-form-input id="Lieu de Ramassage " v-model="new_eleve.new_lieu_ramassage.nom_lieu" required></b-form-input>
              </b-form-group>
          </div>
          <div class="col-1 checkbox" >
            <input type="checkbox" v-model="new_eleve.create_lieu_ramassage" >
            <label for="">
              Autre Lieu de ramassage au lieu
            </label>
      </div>
       </div>
        <h5>Informations Parent Elève</h5>
      <div class="col-12 col-md-6">
          <b-form-group label="Nom Père :">
            <b-form-input v-model="new_eleve.parent.nom_pere" required></b-form-input>
          </b-form-group>
          <div class="text-danger">
            {{new_eleve.parent.nom_pere_error}}
          </div>
      </div>
      <div class="col-12 col-md-6">
      
          <b-form-group label="Tel :">
            <b-form-input v-model="new_eleve.parent.tel_pere" required></b-form-input>
          </b-form-group>
          <div class="text-danger">
            {{new_eleve.parent.tel_pere_error}}
          </div>
      </div>
      <div class="col-12 col-md-6">
          <b-form-group label="Nom Mère :">
            <b-form-input v-model="new_eleve.parent.nom_mere" required></b-form-input>
          </b-form-group>
          <div class="text-danger">
            {{new_eleve.parent.nom_mere_error}}
          </div>
      </div>
      <div class="col-12 col-md-6">
        <b-form-group label="Tel :">
          <b-form-input v-model="new_eleve.parent.tel_mere" required></b-form-input>
        </b-form-group>
        <div class="text-danger">
          {{new_eleve.parent.tel_mere_error}}
        </div>
      </div>
      <h5>Informations Compte d'utilisateur Elève</h5>
    
        <b-button type="submit" variant="" class="btn btn-save">
          <font-awesome-icon :icon="['fas', 'floppy-disk']" />
          <span>Enregistrer</span> 
        </b-button>
      
      </form>
    </div>
  </template>
  
  <script>
  import axios from 'axios';

  
 export default {
    name: 'EleveCreatePage',
    props: {
         baseUrl: String
    },
    watch: {
      'new_eleve.ligne': function(newValue, oldValue) {
        oldValue
           this.new_eleve.new_lieu_ramassage.ligne=newValue
      },
      'new_eleve.lieuligne': function(newValue, oldValue) {
        oldValue
           this.new_eleve.new_lieu_ramassage.lieu_ligne=newValue
      },
    },

   data() {
      return {
          baseUrlPage:`${this.$store.state.baseUrl}/api/transport_scolaire/eleves/`,
          baseUrlEcole:`${this.$store.state.baseUrl}/api/transport_scolaire/ecoles/`,
          baseUrlClasse:`${this.$store.state.baseUrl}/api/transport_scolaire/classes/`,
          baseUrlLigne:`${this.$store.state.baseUrl}/api/transport_scolaire/lignes/`,
          baseUrlLieuLigne:`${this.$store.state.baseUrl}/api/transport_scolaire/lieulignes/`,
          baseUrlLieuRamassage:`${this.$store.state.baseUrl}/api/transport_scolaire/lieuramassages/`,

      new_eleve: {
            nom: '',
            prenoms: '',
            date_naissance: '',
            ecole: '',
            image:null,
            classe: '',
            ligne: '',
            lieuligne: '',
            lieu_ramassage: '',
            create_lieu_ramassage:false,
            create_classe:false,
            new_lieu_ramassage: {
               lieu_ligne:'',
               ligne:'',
               nom_lieu:''
            },
            new_classe:{
                nom:'',
            },
            parent:{
                nom_mere:'',nom_pere:'',
                tel_pere:'',tel_mere:''
            },
            user:{
                 username:"",
                 email:"",
                 password:"",
                 password_verify:"",
            }
        },
        
       

        ecoles: [],
        classes: [],
        lignes: [],
        lieulignes: [],
        lieu_ramassages: [],
           
       };
    },
    methods: {
       
      addEleve() {
            // Créez un nouvel objet Project avec les valeurs du formulaire
            // let error="Cette champ est requis"
            // if (!this.new_eleve.nom) {  
            //      this.new_eleve.nom_error = error
            //      return
            // }
            // if (!this.new_eleve.prenoms) {
            //     this.new_eleve.prenoms_error=error
            //     return
            // }
            // if (!this.new_eleve.date_naissance) {
            //     this.new_eleve.date_naissance_error = error
            //     return
            // }
            // if (!this.new_eleve.ecole) {
            //     this.new_eleve.ecole_error = error
            //     return
            // }
            // if (!this.new_eleve.classe) {
            //   this.new_eleve.classe_error = error
            //   return
            // }
            // if (!this.new_eleve.adresse) {
            //   this.new_eleve.adresse_error = error
            //   return
            // }
            // if (!this.new_eleve.ligne) {
            //   this.new_eleve.ligne_error = error
            //   return
            // }
            // if (!this.new_eleve.ligne) {
            //   this.new_eleve.ligne_error = error
            //   return
            // }
            // Convertir l'objet new_eleve en FormData
              
              
              console.log(this.new_eleve)
            //Envoyer la requête POST à l'API avec les données du projet
            axios.post(this.baseUrlPage, this.new_eleve)
                .then(response => {
                // Traitez la réponse de l'API en conséquence
                console.log(response.data);
                     // Réinitialisez les valeurs du formulaire
     

                this.$router.push('/transport_scolaire/eleve');
                })
              .catch(error => {
              // Gérez les erreurs de la requête
              console.error(error);
              });
      
      
   
       },
       handleImageUploadNewEleve(event){
        const file = event.target.files[0];

        if (file) {
          const reader = new FileReader();
          
          reader.onloadend = () => {
            // Récupérez les données binaires de l'image sous forme de chaîne base64
            const imageData = reader.result.split(',')[1];
            this.new_eleve.image = imageData;
            // {
            //       name: file.name,
            //       type: file.type,
            //       size: file.size,
            //       data: imageData
            // };
          };
          
          // Lisez le contenu de l'image en tant que données binaires
          reader.readAsDataURL(file);
        }
       },
       fecthDataEcole(page){
              // Effectuer la requête GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: false, // Valeur de recherche
                } 
                })
                .then(response => {
                   const data = response.data;
                   this.ecoles = data.map(a => ({ value: a.pk, text: a.repr }));
                })
                .catch(error => {
                     console.error("Une erreur s'est candidate lors de la récupération des ecoles:", error);
                });
          },
          fecthDataClasse(page){
              // Effectuer la requête GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: false, // Valeur de recherche
                } 
                })
                .then(response => {
                   const data = response.data;
                   this.classes = data.map(a => ({ value: a.pk, text: a.repr }));
                })
                .catch(error => {
                     console.error("Une erreur s'est candidate lors de la récupération des classes:", error);
                });
          },
          fecthDataLigne(page){
              // Effectuer la requête GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: false, // Valeur de recherche
                } 
                })
                .then(response => {
                   const data = response.data;
                   this.lignes = data.map(a => ({ value: a.pk, text: a.repr }));
                })
                .catch(error => {
                     console.error("Une erreur s'est candidate lors de la récupération des lignes:", error);
                });
          },
          fecthDataLieuLigne(page){
              // Effectuer la requête GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: false, // Valeur de recherche
                } 
                })
                .then(response => {
                   const data = response.data;
                   this.lieulignes = data.map(a => ({ value: a.pk, text: a.repr }));
                })
                .catch(error => {
                     console.error("Une erreur s'est candidate lors de la récupération des lignes:", error);
                });
          },
          fecthDataLieuRamassage(page){
              // Effectuer la requête GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: false, // Valeur de recherche
                } 
                })
                .then(response => {
                   const data = response.data;
                   this.lieu_ramassages = data.map(a => ({ value: a.pk, text: a.repr }));
                })
                .catch(error => {
                     console.error("Une erreur s'est candidate lors de la récupération des lignes:", error);
                });
          },
  
    },
    mounted() {

       this.fecthDataEcole(this.baseUrlEcole);
       this.fecthDataClasse(this.baseUrlClasse);
       this.fecthDataLigne(this.baseUrlLigne);
       this.fecthDataLieuLigne(this.baseUrlLieuLigne);
       this.fecthDataLieuRamassage(this.baseUrlLieuRamassage);

  }




  }
</script>

  <!-- Add "scoped" attribute to limit CSS to this component only -->
  <style scoped>
  .col-2.checkbox input{
        height: 30px;
        width: 30px;
        border: 1px solid red !important;
         background-color: #000000 !important;
         font-size: 40px;
  }
    .col-2.checkbox{
       
        display:flex;
        align-items: center;
        gap: 3px;
      }
      #checkbox{
         border: 1px solid red !important;
         background-color: #000000 !important;
      }
      .form-check-input{
       
      }
  </style>
  
