
<template>
    <div class="container">
      
      <form @submit.prevent="editChauffeur" class="row form-form form-75">
  
        <div class="title">
          <h4>
            <span>Modification</span> <span>Chauffeur</span>
          </h4>
          <div class="line">
          
          </div>
        </div>
  
        <h5>Informations  Chauffeur</h5>
        <div class="col-12   col-md-6">
          <b-form-group label="nom :">
            <b-form-input id="nom" v-model="edit_chauffeur.nom" required></b-form-input>
          </b-form-group>
        </div>
        <div class="col-12 col-md-6">
          <b-form-group label="Prénom :">
            <b-form-input id="prenoms" v-model="edit_chauffeur.prenoms" required></b-form-input>
          </b-form-group>
        </div>
      
        <div class="col-12">
          <b-form-group label="Email :">
            <b-form-input id="email" v-model="edit_chauffeur.email" type="email" required></b-form-input>
          </b-form-group>
        </div>
      
        <div class="col-12">
          <b-form-group label="Téléphone :">
            <b-form-input v-model="edit_chauffeur.tel" required></b-form-input>
          </b-form-group>
        </div>
    
        <b-button type="submit" variant="" class="btn btn-save">
          <font-awesome-icon :icon="['fas', 'floppy-disk']" />
          <span>Enregistrer les modifications</span> 
        </b-button>
      
      </form>
    </div>
  </template>
  
  <script>
  import axios from 'axios';
 
  
  export default {
    name: 'ChauffeurCreatePage',
    mounted() {
        const encryptedId = this.$route.params.id; // Récupère l'ID crypté depuis l'URL
        const decryptedId = this.$decryptId(encryptedId); // Décrypte l'ID
        console.log(decryptedId);

        this.url=`${this.$store.state.baseUrl}/api/transport_scolaire/ecoles/${decryptedId}/`
        // console.log(url)
        this.fecthData(this.url)
    },
  
    data() {
    return {
      url:'',
      baseUrlPage:`${this.$store.state.baseUrl}/api/transport_scolaire/ecoles/`,
      edit_chauffeur: {
        nom: '',
        prenoms: '',
        email: '',
        tel: "",
      
      },
    
    };
  },
    methods: {
      fecthData(page){
              // Effectuer la requête GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: true, // Valeur de recherche
                } 
                })
                .then(response => {
                   const data = response.data;
                   this.edit_chauffeur = {
                    ...data,
                   }
                })
                .catch(error => {
                     console.error("Une erreur s'est candidate lors de la récupération des chauffeurs:", error);
                });
          },
      editChauffeur() {
  
  
            // Créez un nouvel objet Project avec les valeurs du formulaire
      
  
  
  
            // Envoyer la requête POST à l'API avec les données du projet
            axios.put(this.url, this.edit_chauffeur)
                .then(response => {
                // Traitez la réponse de l'API en conséquence
                console.log(response.data);
                     // Réinitialisez les valeurs du formulaire
       
  
                this.$router.push('/transport_scolaire/chauffeur');
                })
                .catch(error => {
                // Gérez les erreurs de la requête
                console.error(error);
                });
      
      
   
       }
  
    },
  
  
  
  
  }
  </script>
  
  <!-- Add "scoped" attribute to limit CSS to this component only -->
  <style scoped>
  .popup-task{
    position: fixed;
    z-index: 100;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 100%;
    max-width: 400px;
    background-color: #383838;
    padding: 20px;
    color: #fff;
    box-shadow: rgba(0, 0, 0, 0.1);
    border-radius: 5px;
  }
  h3 {
    margin: 40px 0 0;
  }
  ul {
    list-style-type: none;
    padding: 0;
  }
  li {
    display: inline-block;
    margin: 0 10px;
  }
  a {
    color: #42b983;
  }
  </style>
  
  