
<template>
  <div class="container">
    
    <form @submit.prevent="EditItineraire" class="row form-form form-75">

      <div class="title">
        <h4>
          <span>Ajout</span> <span>Itineraire</span>
        </h4>
        <div class="line">
        
        </div>
      </div>

      <h5>Informations  Itineraire</h5>
      <div class="col-12 col-md-12">
          <b-form-group label="Ligne :">
            <b-form-select @change="handleSelectedLigneChange" v-model="edit_itineraire.ligne" :options="lignes" required></b-form-select>
          </b-form-group>
      </div>
      
      <div class="col-12">
        <div class="table-responsive">
          <table class="table">
            <thead>
              <tr>
                <th>Numéro d'immatriculation</th>
                <th>Action</th>
           
              </tr>
            </thead>
            <tbody>
              
                <tr class="" v-for="bus in buss" :key="bus.pk">
                  <td>
                    {{bus.repr}}
                  </td>
                  <td>

                      <input type="checkbox" name=""  v-if="isChecked(bus.pk)" checked  @input="handleChangeBusCheckBox(bus.pk)">
                      <input type="checkbox" name=""  v-if="!isChecked(bus.pk)"  @input="handleChangeBusCheckBox(bus.pk)">
                   
                    </td>
                 
                </tr>
       
            </tbody>
          </table>
        </div>
       
      </div>
      <h5>Horaire De l'Itineraire</h5>
      <div class="col-12">
        <label for="">Inverser Lieu Ligne</label>
        <input type="checkbox" name="" @change="handleLigneInverseChange" v-model="edit_itineraire.ligne_inverse" id="">
    </div>
      <div class="col-12 ">
           
        <div class="table-responsive">

          <table class="table">
                <thead>
                  <tr>
                      <th>Lieu</th>
                      <th>Heure Début</th>
                      <th>Heure Fin</th>
                    </tr>
                </thead>
                <tbody>
                <template v-if="edit_itineraire.selectedLigne">
                    <tr  v-for="(ordrelieu,idx) in edit_itineraire.selectedLigne.ordrelieus" :key="idx">
                      <td><input v-model="edit_itineraire.horaires[idx].pointArret" hidden required>{{ordrelieu.pointArret.repr}}</td>
                      <td><input v-model="edit_itineraire.horaires[idx].heureDebut" required type="time"></td>
                      <td><input v-model="edit_itineraire.horaires[idx].heureFin" required type="time"></td>
                    </tr>
                  </template>
                  <template v-else>
                    <tr>
                      <td colspan="3">La variable 'ligne' n'est pas définie.</td>
                    </tr>
                  </template>
                </tbody>
              </table>
            </div>    
       </div>
    
      <b-button type="submit" variant="" class="btn btn-save">
        <font-awesome-icon :icon="['fas', 'floppy-disk']" />
        <span>Enregistrer</span> 
      </b-button>
    
    </form>
  </div>
</template>

<script>
import axios from 'axios';


export default {
  name: 'ItineraireCreatePage',

  mounted() {

    const encryptedId = this.$route.params.id; // Récupère l'ID crypté depuis l'URL
    const decryptedId = this.$decryptId(encryptedId); // Décrypte l'ID
    console.log(decryptedId);

    this.url=`${this.$store.state.baseUrl}/api/transport_scolaire/itineraires/${decryptedId}/`

    this.fecthData(this.url);
    this.fecthDataLigne(this.baseUrlLigne);
    this.fecthDataBus(this.baseUrlBus);
  
  },

 data() {
    return {

      url:'',
      baseUrlPage:`${this.$store.state.baseUrl}/api/transport_scolaire/itineraires/`,
      baseUrlLigne:`${this.$store.state.baseUrl}/api/transport_scolaire/lignes/`,
      baseUrlBus:`${this.$store.state.baseUrl}/api/transport_scolaire/buss/`,
      lignes:[],
      buss:[],

      edit_itineraire: {
          nom: '',
          horaires:[],
          ligne:'',
          ligne_inverse:false,
          selectedLigne:'',
          busassignations:[],
      },
      
     };
  },
  methods: {
    isChecked(bus) {
        const assignedBuses = this.edit_itineraire.busassignations.map(item => item.bus);
        // console.log("assignedBuses",assignedBuses,assignedBuses.includes(bus),bus)
       return assignedBuses.includes(bus);

      },
    handleSelectLieuClickDelete(num_ordre,event){
      event
      const index = this.edit_itineraire.ordrelieus.findIndex(a => a.num_ordre === num_ordre);
      if (index !== -1) {
        this.edit_itineraire.ordrelieus.splice(index, 1);
      }
    },
    fecthData(page){
              // Effectuer la requête GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: true, // Valeur de recherche
                } 
                })
                .then(response => {
                   const data = response.data;
                   this.edit_itineraire = {
                    ...data,
                    ligne:data.ligne.pk,
                   
                   }
                   this.edit_itineraire.selectedLigne=data.ligne
                  //  this.edit_itineraire.selectedLigne.ordrelieus= data.horaires.map((ordrelieu)=>{ 
                  //   console.log(ordrelieu)         
                  //         return ordrelieu
                  //   })
                    console.log(this.edit_itineraire.selectedLigne.ordrelieus)


                    


                    this.edit_itineraire.busassignations= data.busassignations.map((busassignation)=>{ 
                          
                        busassignation.bus=busassignation.bus.pk         
                        return busassignation

                    })

                    let dataHoraires = JSON.parse(JSON.stringify(data.horaires));
                    console.log(dataHoraires)
                    this.edit_itineraire.horaires= data.horaires.map((horaire)=>{ 
                      console.log(horaire)
                          horaire.pointArret=horaire.pointArret.pk      
                          return horaire
  
                      })

                    console.log('here',dataHoraires)
                    this.edit_itineraire.selectedLigne.ordrelieus=dataHoraires
                    if(this.edit_itineraire.ligne_inverse){
                         this.edit_itineraire.selectedLigne.ordrelieus=this.edit_itineraire.selectedLigne.ordrelieus.reverse()
                        //  this.edit_itineraire.horaires=this.edit_itineraire.horaires.reverse()
                    }
                    console.log( this.edit_itineraire.selectedLigne.ordrelieus)
                })
                .catch(error => {
                     console.error("Une erreur s'est produit lors de la récupération itineraire:", error);
                });
          },
    handleChangeBusCheckBox(bus){
          let index=this.edit_itineraire.busassignations.findIndex((el)=>el.bus==bus)
          if(index==-1){  
               this.edit_itineraire.busassignations.push({
                   bus:bus,
               })
          }else{
              this.edit_itineraire.busassignations.splice(index,1)
          }
         
    },
    handleLigneInverseChange(){
         if(this.edit_itineraire.selectedLigne){
           this.edit_itineraire.selectedLigne.ordrelieus=this.edit_itineraire.selectedLigne.ordrelieus.reverse();
          //  this.edit_itineraire.horaires=this.edit_itineraire.horaires.reverse()
         }
           
    },
    handleSelectedLigneChange(event) {
    
      const selectItem=this.lignes.find(pr=>pr.pk==event)
      const n=selectItem.ordrelieus.length
    
      this.edit_itineraire.horaires = Array.from({ length: n }, (_, i) => (
        {
           idx: i,
           pointArret:selectItem.ordrelieus[i].lieu_ligne.pk, 
        }));
      console.log(this.edit_itineraire.horaires)
      this.edit_itineraire.selectedLigne=selectItem

      
    },

    fecthDataLigne(page){
              // Effectuer la requête GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: false, // Valeur de recherche
                    special_detail_manytoone_class:"['ordrelieus',]"
              } 
              })
                .then(response => {
                   const data = response.data;
                   this.lignes = data.map(a => ({...a, value: a.pk, text: a.repr}));
                   console.log(this.lignes)
                })
                .catch(error => {
                     console.error("Une erreur s'est produit lors de la récupération des lignes :" , error);
                });
      },
      fecthDataBus(page){
              // Effectuer la requête GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: false, // Valeur de recherche
                    
              } 
              })
                .then(response => {
                   const data = response.data;
                   this.buss = data.map(a => ({...a, value: a.pk, text: a.repr}));
                   console.log(this.buss)
                })
                .catch(error => {
                     console.error("Une erreur s'est produite lors de la récupération des buss :" , error);
                });
      },

      addOrderLieu() {
        let orde = 1;
        if (this.edit_itineraire.ordrelieus.length > 0) {
          orde = this.edit_itineraire.ordrelieus[this.edit_itineraire.ordrelieus.length - 1].num_ordre + 1;
        }

        console.log(orde);
        const newOrderLieu = {
          num_ordre: orde,
          lieu_itineraire: ''
        };
        this.edit_itineraire.ordrelieus.push(newOrderLieu);
        console.log(this.edit_itineraire.ordrelieus);
      },
    EditItineraire() {
        console.log(this.edit_itineraire)
        console.log(this.edit_itineraire)
        axios.put(this.url, this.edit_itineraire)
                .then(response => {
                // Traitez la réponse de l'API en conséquence
                console.log(response.data);
                     // Réinitialisez les valeurs du formulaire

                this.$router.push('/transport_scolaire/itineraire');
                })
                .catch(error => {
                // Gérez les erreurs de la requête
                console.error(error);
                });
     }

  },




}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.lieu{


  border: 1px solid rgb(43, 2, 21);
  box-shadow: rgba(0, 0, 0, 0.1);
  border-radius: 5px;
}
.lieu .action{
  color:  red;
  display: flex;
  align-items: center;
  font-size: 22px;
}

</style>
