
import { createStore } from 'vuex'





const store = createStore({
    state: {
        user: null, // Initialisez la propriété user à null
        baseUrl:"http://localhost:8000",
      },
      mutations: {
        setUser(state, user) {
          state.user = user;
        },
      },
      actions: {
        updateUser({ commit }, user) {
          commit('setUser', user);
        },
      },
})



export default store;