// main.js

import { createApp } from 'vue';
import App from './App.vue';
import router from './router';
import store from './store';
import BootstrapVue from 'bootstrap-vue-3';
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap-vue-3/dist/bootstrap-vue-3.css';
import Particles from "vue3-particles";
import { library } from "@fortawesome/fontawesome-svg-core";
import { faUserPlus, faChildren, faPersonBreastfeeding, faFilter,
   faFilePen, faFloppyDisk, faRoad, faLocationDot, faCircleStop, faPhone,
   faMagnifyingGlass, faBus, faIdCard, faX, faPlus, faPowerOff,
   faKey, faChevronDown, faUser, faChevronRight, faChevronLeft, faLock, faBars } from "@fortawesome/free-solid-svg-icons"; 
import { FontAwesomeIcon } from "@fortawesome/vue-fontawesome";
import AES from 'crypto-js/aes';
import Utf8 from 'crypto-js/enc-utf8';

library.add(faUserPlus, faChildren, faPersonBreastfeeding, faRoad,faPhone,
   faBus, faFilePen, faX, faPlus, faFloppyDisk, faLocationDot, faCircleStop, faMagnifyingGlass, faFilter, faIdCard, faKey, faChevronDown, faUser, faLock, faPowerOff, faChevronRight, faChevronLeft, faBars);

// Clé de chiffrement (vous pouvez la générer aléatoirement ou utiliser une clé spécifique)
const encryptionKey = 'MaCleDeChiffrement';


const encryptId = function(id) {
  const ciphertext = AES.encrypt(id.toString(), encryptionKey).toString();
  const encodedCiphertext = btoa(ciphertext);
  return encodedCiphertext;
};
// Fonction pour décrypter un ID crypté
const decryptId = function(encodedCiphertext) {
  const ciphertext = atob(encodedCiphertext);
  const decryptedId = AES.decrypt(ciphertext, encryptionKey).toString(Utf8);
  return decryptedId;
};

const app = createApp(App);
app.use(Particles);
app.component("font-awesome-icon", FontAwesomeIcon);

app.use(BootstrapVue);


// Ajouter les fonctions de cryptage au prototype de Vue
app.config.globalProperties.$encryptId = encryptId;
app.config.globalProperties.$decryptId = decryptId;



app.config.globalProperties.$obtainTokenUrl = `${store.state.baseUrl}/api/token/`;
app.config.globalProperties.$refreshTokenUrl = `${store.state.baseUrl}/api/token/refresh`;







import axios from "axios";


router.beforeEach(async (to, from, next) => {



  try {
    if(localStorage.getItem('access')){
         axios.defaults.headers.common['Authorization'] = `Bearer ${localStorage.getItem('access')}`;
    }else{
      axios.defaults.headers.common['Authorization'] =null;
    }
    // Effectuer une requête GET pour vérifier l'authentification
    const response = await axios.get(`${store.state.baseUrl}/api/token/check/`); // Remplacez '/check-auth' par votre URL de vérification d'authentification
    
    console.log(response)

    store.commit('setUser', response.data.user);

    console.log(store.state.user,response.data.user)
    console.log(axios.defaults.headers.common['Authorization'])
    if (to.meta.requiresAuth) {
       response.data.user.role?next():next('/login');
    } else if(to.meta.requiresAdmin){
       response.data.user.role=="admin"?next():next('/login');
    }
    else {
      next(); // La route n'exige pas d'authentification, continuez la navigation normalement
    } 
    } catch (error) {
        store.commit('setUser', null);
        console.error(error)
        next();
    }

});





app.use(store).use(router)
app.mount('#app');