
<template>
    <div class="container">
      <h3>Gestion de projects</h3>
      <form @submit.prevent="addParentt" class="row">


        <div class="col-4">
            <b-form-group label="nom :">
                 <b-form-input id="nom" v-model="newParentt.nom" required></b-form-input>
            </b-form-group>
        </div>

        <div class="col-4">
            <b-form-group label="agge :">
                 <b-form-input id="agge" v-model="newParentt.agge" required></b-form-input>
            </b-form-group>
        </div>

        <b-form-group>
                <b-button type="submit" variant="success">Save</b-button>
        </b-form-group>
    </form>
    </div>
  </template>
  
  <script>
  import axios from 'axios';
  const baseUrl="localhost:8000"
  
 export default {
    name: 'ParenttCreatePage',
    mounted() {
        //   this.maFonction("created");
    },

   data() {
      return {

  
          newParentt: {
nom: '',
agge: '',
},
           
       };
    },
    methods: {
       
      addParentt() {


            // Créez un nouvel objet Project avec les valeurs du formulaire
            const newParentt = {
nom: this.newParentt.nom,
agge: this.newParentt.agge,
};


            // Envoyer la requête POST à l'API avec les données du projet
            axios.post(`http://${baseUrl}/api/parentt`, newParentt)
                .then(response => {
                // Traitez la réponse de l'API en conséquence
                console.log(response.data);
                     // Réinitialisez les valeurs du formulaire
          this.newParentt.nom = null ;
          this.newParentt.agge = null ;

                this.$router.push('/parentt');
                })
                .catch(error => {
                // Gérez les erreurs de la requête
                console.error(error);
                });
      
      
   
       }
  
    },




  }
</script>

  <!-- Add "scoped" attribute to limit CSS to this component only -->
