    width: 100%;
    max-width: 400px;
    background-color: #383838;
    padding: 20px;
    color: #fff;
    box-shadow: rgba(0, 0, 0, 0.1);
    border-radius: 5px;
}
  h3 {
    margin: 40px 0 0;
  }
  ul {
    list-style-type: none;
    padding: 0;
  }
  li {
    display: inline-block;
    margin: 0 10px;
  }
  a {
    color: #42b983;
  }
  </style>
  
