  <script>

    import axios from 'axios';

  export default {
    name: 'HomePage',
    props: {
      msg: String
    },
    mounted() {
      this.fecthDataLigne(this.baseUrlLigne);
    },
    data(){
        return {
          baseUrlLigne:`${this.$store.state.baseUrl}/api/transport_scolaire/lignes/`,
          lignes:[],
        }
    },
    methods:{
      fecthDataLigne(page){
              // Effectuer la requête GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: false, // Valeur de recherche
                    special_detail_manytoone_class:"['ordrelieus',]"
              } 
              })
                .then(response => {
                   const data = response.data;
                   this.lignes = data.map(a => ({...a, value: a.pk, text: a.repr}));
                   console.log(this.lignes)
                })
                .catch(error => {
                     console.error("Une erreur s'est produite lors de la récupération des lignes :" , error);
                });
      },

    }
  }
