
<template>
  <div id="nav">
    
    
    <div v-if="!isAdmin && !isLogin">
      <HeaderComponentSite/>
        <router-view v-slot="{ Component }">
                <transition name="fade" mode="out-in">
                  <component :is="Component" />
                </transition>
            </router-view>
    </div>
    <div v-if="!isAdmin && isLogin">
      
        <router-view v-slot="{ Component }">
                <transition name="fade" mode="out-in">
                  <component :is="Component" />
                </transition>
            </router-view>
    </div>
    <main v-if="isAdmin" :class="{'active':mainActive,'active2':mainActive2}">
      <div>
     
     </div>
      <AsideComponent ref="asideComponent"/>
      <HeaderComponent @mounted="handleWindowSizeChange" @menu-click="handleMenuClick"/>

      <section>

        <router-view v-slot="{ Component }">
            <transition name="fade" mode="out-in">
              <component :is="Component" />
            </transition>
         </router-view>

      </section>
    </main>

  </div>
</template>

<script>
import HeaderComponent from './components/Header.vue';
import HeaderComponentSite from './components/HeaderSite.vue';
import AsideComponent from './components/Aside.vue';


export default {
    name: 'App',
    setup() {
   

  },
    data(){
      return {
        baseUrl:"http://localhost:8000",
        mainActive:false,
        mainActive2:false,
windowWidth: window.innerWidth,
        windowHeight: window.innerHeight,
      }
    },
    components: {
    HeaderComponent,AsideComponent,HeaderComponentSite
      },
mounted() {
                this.handleWindowSizeChange()
                window.addEventListener('resize', this.handleWindowSizeChange);
    },
    beforeUnmount() {
                // Remove the event listener when
                // the component is destroyed
                // window.removeEventListener('resize', this.handleWindowSizeChange);
      },
    computed: {
          isAdmin() {
            // const excludedPages = ['Home','Login','Inscription','InscriptionList'];
            const currentRoute = this.$route.path;
            return currentRoute.includes("transport_scolaire");
          },
          isLogin() {
            return  this.$route.name=='Login'
          }
        },
   
    methods:{
handleWindowSizeChange() {
                    console.log("test",window.innerWidth,window.innerHeight)
                    // Update data properties with 
                    // new window dimensions
                    this.windowWidth = window.innerWidth;
                    this.windowHeight = window.innerHeight;
 
                    // Additional logic based on
                    // window size changes
                    if(this.windowWidth<1100 && this.$refs.asideComponent){
                         this.mainActive2=true;
                         this.mainActive=true;
                     
                         this.$refs.asideComponent.asideActive=true;
                         this.$refs.asideComponent.asideActive2=true;
                    }else if(this.$refs.asideComponent){
                        this.mainActive2=false;
                        this.mainActive=false;
                        this.$refs.asideComponent.asideActive=false;
                        this.$refs.asideComponent.asideActive2=false;
                    }
                },
      handleMenuClick() {
        if(this.mainActive==true){
          this.mainActive=false
          this.mainActive2=false
        }else{
          this.mainActive=true
          this.mainActive2=true
        }

        this.$refs.asideComponent.handleMenuClick();
      }
    },
}

</script>

