<template>
     <!-- Analyses -->
     <div class="analyse">
        <div class="sales">
            <div class="status">
                <div class="info">
                    <h3>Total Eleve Inscrits</h3>
                    <h1>{{counts.eleve}}</h1>
                </div>

                <div class="progresss">
                    <svg>
                        <circle cx="38" cy="38" r="36"></circle>
                    </svg>

                    <div class="percentage">
                        <p>+81%</p>
                    </div>
                </div>

            </div>
        </div>
        <div class="visits">
            <div class="status">
                <div class="info">
                    <h3>Inscriptions en Attente</h3>
                    <h1>{{counts.inscription_en_attente}}</h1>
                </div>
                <div class="progresss">
                    <svg>
                        <circle cx="38" cy="38" r="36"></circle>
                    </svg>
                    <div class="percentage">
                        <p>-48%</p>
                    </div>
                </div>
            </div>
        </div>
        <div class="searches">
            <div class="status">
                <div class="info">
                    <h3>Inscriptions Annulés</h3>
                    <h1>{{counts.inscription_annule}}</h1>
                </div>
                <div class="progresss">
                    <svg>
                        <circle cx="38" cy="38" r="36"></circle>
                    </svg>
                    <div class="percentage">
                        <p>+21%</p>
                    </div>
                </div>
            </div>
        </div>
    </div>

    <div class="row graphs">
        <div class="col-12 col-md-6 graph" v-if="chartData!={}">
            <h2 class="mb-4 text-center">Ligne / Elève</h2>
            <Bar id="my-chart-id" :options="chartOptions" :data="chartDataB()" />
        </div>
        <div class="col-12 col-md-6 graph" v-if="chartData!={}">
            <h2 class="mb-4 text-center">Elève /Lieu</h2>
            <Bar id="my-chart-id" :options="chartOptions" :data="chartDataB()" />
        </div>
    </div>
    <!-- End of Analyses -->
  </template>
  
  <script>

import axios from '../../apiService';
// import { BarChart } from 'vue-chart-3';
import { Bar } from 'vue-chartjs'
import { Chart as ChartJS, Title, Tooltip, Legend, BarElement, CategoryScale, LinearScale } from 'chart.js'
ChartJS.register(Title, Tooltip, Legend, BarElement, CategoryScale, LinearScale)

  export default {
    name: 'AboutPage',
    props: {
      msg: String
    },
    components: { Bar },
    data(){
        return {
            baseUrlPage:`${this.$store.state.baseUrl}/api/counts/`,
            counts:{

            },

            chartData:{
                labels: [ 'January', 'February', 'March' ],
                            backgroundColor: "#f87979",
                            datasets: [ 
                                { data: [40, 20, 12],
                                backgroundColor: ['#6C9BCF', '#1B9C85', '#FF0060',] } ,
                                ]
            },

            chartOptions: {
                responsive: true
            }
        }
    },

    methods: {

        fecthData(page){
                // Effectuer la requête GET vers l'API en utilisant Axios
                axios.get(page,{ params: {
                        paginate: true, // Valeur de recherche
                    } 
                    })
                    .then(response => {
                    const data = response.data;
                    this.counts = data
                    
                    this.chartData.labels=data.ligne_eleve.labels;
                    this.chartData.datasets.data=data.ligne_eleve.values;
                    this.chartData.datasets.backgroundColor=data.ligne_eleve.colors;
                    this.chartData.backgroundColor= "#f87979";

                    
                    })
                    .catch(error => {
                        console.error("Une erreur s'est produit lors de la récupération des élèves:", error);
                    });
            },
        
            chartDataB(){
                let a=2
                while(this.counts=={}){
                        a
                }

                return this.chartData;
            }
        }
        ,

        mounted() {
        this.fecthData(this.baseUrlPage)
        }
  }
  </script>
  
