
<template>
  <div class="container">
    
    <form @submit.prevent="editLigne" class="row form-form form-75">

      <div class="title">
        <h4>
          <span>Ajout</span> <span>Ligne</span>
        </h4>
        <div class="line">
        
        </div>
      </div>

      <h5>Informations  Ligne</h5>
      <div class="col-12   col-md-12">
        <b-form-group label="nom :">
          <b-form-input id="nom" v-model="edit_ligne.nom" required></b-form-input>
        </b-form-group>
      </div>
      <h5>Ajouter Les lieux du ligne</h5>
      <div class="col-12 row">
            <transition-group name="fade" tag="div" class="row"  mode="out-in">
              <div class="col-12 col-md-4 lieu" v-for="(ordrelieu,idx) in edit_ligne.ordrelieus" :key="idx">
                  <div class="row">
                      <b-form-group class="col-10 col-md-9" label="Lieu Ligne :" >
                          <select class="form-select" v-model="edit_ligne.ordrelieus[idx].lieu_ligne" @input="handleSelectLieuChange(ordrelieu.num_ordre, $event)" required>
                            <option value="">Sélectionnez...</option> <!-- Option vide ajoutée -->
                            <option  :selected="option.value === ordrelieu.lieu_ligne"
                             v-for="option in lieu_lignes" :value="option.value" :key="option.value">{{ option.text }}</option>
                          </select>             
                      </b-form-group>
                      <div class="col-2 col-md-3 action">
                              
                            <font-awesome-icon @click="handleSelectLieuClickDelete(ordrelieu.num_ordre,$event)" :icon="['fas', 'x']" />
                      </div>
                  </div>
              </div>
          </transition-group>
              <b-button @click="addOrderLieu" variant="" class="btn btn-plus">
                <font-awesome-icon :icon="['fas', 'plus']" />
                <span>Plus</span> 
               </b-button>
        
       </div>
    
    
     
    
   
  
      <b-button type="submit" variant="" class="btn btn-save">
        <font-awesome-icon :icon="['fas', 'floppy-disk']" />
        <span>Enregistrer</span> 
      </b-button>
    
    </form>
  </div>
</template>

<script>
import axios from 'axios';


export default {
  name: 'ChauffeurCreatePage',

  mounted() {


    const encryptedId = this.$route.params.id; // Récupère l'ID crypté depuis l'URL
    const decryptedId = this.$decryptId(encryptedId); // Décrypte l'ID
    console.log(decryptedId);

    this.url=`${this.$store.state.baseUrl}/api/transport_scolaire/lignes/${decryptedId}/`
    
    this.fecthData(this.url);
    this.fecthDataLieuLigne(this.baseUrlLieuLigne);
      
    },
 data() {
    return {

      baseUrlPage:`${this.$store.state.baseUrl}/api/transport_scolaire/lignes/`,
      baseUrlLieuLigne:`${this.$store.state.baseUrl}/api/transport_scolaire/lieulignes/`,
      lieu_lignes:[],
      edit_ligne: {
          nom: '',
          ordrelieus:[]
      },

     };
  },
  methods: {

        fecthData(page){
              // Effectuer la requête GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: true, // Valeur de recherche
                } 
                })
                .then(response => {
                   const data = response.data;
                   this.edit_ligne = {
                    ...data,
                   }
                   const ordrelieus=data.ordrelieus.map((ordrelieu)=>{
                         ordrelieu.lieu_ligne=ordrelieu.lieu_ligne.pk
                         return ordrelieu
                   })
                   this.edit_ligne.ordrelieus=ordrelieus
                  console.log(this.edit_ligne.ordrelieus)
                })
                .catch(error => {
                     console.error("Une erreur s'est produit lors de la récupération des lignes:", error);
                });
          },

    handleSelectLieuClickDelete(num_ordre,event){
      event
      const index = this.edit_ligne.ordrelieus.findIndex(a => a.num_ordre === num_ordre);
      if (index !== -1) {
        this.edit_ligne.ordrelieus.splice(index, 1);
      }
    },
    handleSelectLieuChange(num_ordre,event) {
     
      const target=event.target;
      let lieu_ligne=target.value;
      if(this.edit_ligne.ordrelieus.findIndex(a=>a.lieu_ligne==lieu_ligne)!=-1){
        lieu_ligne=''
          target.selectedIndex =-1
      }
      const newOrderLieu={
            num_ordre:num_ordre,
            lieu_ligne:lieu_ligne
        }
        console.log(target)
    
      console.log("La sélection a changé pour l'ordre", this.edit_ligne.ordrelieus,newOrderLieu);
    },

    fecthDataLieuLigne(page){
              // Effectuer la requête GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: false, // Valeur de recherche
              } 
              })
                .then(response => {
                   const data = response.data;
                   this.lieu_lignes = data.map(a => ({ value: a.pk, text: a.repr }));
                })
                .catch(error => {
                     console.error("Une erreur s'est candidate lors de la récupération des types:", error);
                });
      },
      addOrderLieu() {
        let orde = 1;
        if (this.edit_ligne.ordrelieus.length > 0) {
          orde = this.edit_ligne.ordrelieus[this.edit_ligne.ordrelieus.length - 1].num_ordre + 1;
        }

        console.log(orde);
        const newOrderLieu = {
          num_ordre: orde,
          lieu_ligne: ''
        };
        this.edit_ligne.ordrelieus.push(newOrderLieu);
        console.log(this.edit_ligne.ordrelieus);
      },
    editLigne() {


          // Envoyer la requête PUT à l'API avec les données du ligne
          axios.put(this.url, this.edit_ligne)
                .then(response => {
                // Traitez la réponse de l'API en conséquence
                console.log(response.data);
                     // Réinitialisez les valeurs du formulaire

                this.$router.push('/transport_scolaire/ligne');
                })
                .catch(error => {
                // Gérez les erreurs de la requête
                console.error(error);
                });
     }

  },




}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.lieu{


  border: 1px solid rgb(43, 2, 21);
  box-shadow: rgba(0, 0, 0, 0.1);
  border-radius: 5px;
}
.lieu .action{
  color:  red;
  display: flex;
  align-items: center;
  font-size: 22px;
}

</style>

