
<template>
    <div class="container">
      
      <form @submit.prevent="addBus" class="row form-form form-75">

        <div class="title">
          <h4>
            <span>Ajout</span> <span>Bus</span>
          </h4>
          <div class="line">
          
          </div>
        </div>

        <h5>Informations  Bus</h5>
        <div class="col-12   col-md-6">
          <b-form-group label="immatriculation :">
            <b-form-input id="immatriculation" v-model="new_bus.immatriculation" required></b-form-input>
          </b-form-group>
          <div class="text-danger">

          </div>
        </div>
        <div class="col-12 col-md-6">
          <b-form-group label="nombre_place :">
            <b-form-input id="nombre_place" v-model="new_bus.nbr_place" required></b-form-input>
          </b-form-group>
        </div>
      
        
       
        <div class="col-12 col-md-6">
          <b-form-group label="Chauffeur :">
            <b-form-select v-model="new_bus.chauffeur" :options="chauffeurs" ></b-form-select>
          </b-form-group>
        </div>
        <div class="col-12 col-md-6">
          <b-form-group label="Type Vehicule :">
            <b-form-select v-model="new_bus.type" :options="types" required></b-form-select>
          </b-form-group>
        </div>
        <div class="col-12 col-md-6">
          <b-form-group label="Coleur :">
            <b-form-input id="couleur" v-model="new_bus.couleur" type="color" required></b-form-input>
          </b-form-group>
        </div>

       
      


      
    
        <b-button type="submit" variant="" class="btn btn-save">
          <font-awesome-icon :icon="['fas', 'floppy-disk']" />
          <span>Enregistrer</span> 
        </b-button>
      
      </form>
    </div>
  </template>
  
  <script>
  import axios from 'axios';

  
 export default {
    name: 'BusCreatePage',


   data() {
      return {

          baseUrlPage:`${this.$store.state.baseUrl}/api/transport_scolaire/buss/`,
          baseUrlType:`${this.$store.state.baseUrl}/api/transport_scolaire/typevehicules/`,
          baseUrlChauffeur:`${this.$store.state.baseUrl}/api/transport_scolaire/chauffeurs/`,

          new_bus: {
            immatriculation: '',
            immatriculation_error:'',
            nbr_place: '',
            couleur: '#000000',
            type: '',
            chauffeur: '',
        },
        types: [],
        chauffeurs: [],
           
       };
    },
    methods: {
      fecthDataChauffeur(page){
              // Effectuer la requête GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: false, // Valeur de recherche
              } 
              })
                .then(response => {
                   const data = response.data;
                   console.log(data)
                   this.chauffeurs = data.map(a => ({ value: a.pk, text: a.repr }));
                })
                .catch(error => {
                     console.error("Une erreur s'est candidate lors de la récupération des chauffeurs:", error);
                });
      },
      fecthDataType(page){
              // Effectuer la requête GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: false, // Valeur de recherche
              } 
              })
                .then(response => {
                   const data = response.data;
                   this.types = data.map(a => ({ value: a.pk, text: a.repr }));
                })
                .catch(error => {
                     console.error("Une erreur s'est candidate lors de la récupération des types:", error);
                });
      },
      addBus() {
            // Créez un nouvel objet Project avec les valeurs du formulaire
            // let error="Cette champ est requis"
            // if (!this.new_bus.immatriculation) {  
            //      this.new_bus.immatriculation_error = error
            //      return
            // }
            // if (!this.new_bus.nbr_place) {
            //     this.new_bus.nbr_place_error=error
            //     return
            // }
            // if (!this.new_bus.couleur) {
            //     this.new_bus.couleurerror = error
            //     return
            // }
            // if (!this.new_bus.type) {
            //     this.new_bus.type_error = error
            //     return
            // }
            // if (!this.new_bus.chauffeur) {
            //   this.new_bus.chauffeur_error = error
            //   return
            // }


            // Envoyer la requête POST à l'API avec les données du projet
            axios.post(this.baseUrlPage, this.new_bus)
                .then(response => {
                // Traitez la réponse de l'API en conséquence
                console.log(response.data);
                     // Réinitialisez les valeurs du formulaire
     

                this.$router.push('/transport_scolaire/bus');
                })
              .catch(error => {
              // Gérez les erreurs de la requête
              console.error(error);
              });
      
      
   
       },
  
    },
    mounted() {
      this.fecthDataType(this.baseUrlType);
      this.fecthDataChauffeur(this.baseUrlChauffeur);
    }




  }
</script>

