<template>
  <div>
    
    <div class="cont-sep">
      <div class="title">
        <h4>
             <span>Liste</span> <span>D'inscription</span>
        </h4>
        <div class="line">
         
        </div>
      </div>
      <div class="actions">
        <router-link to="/inscription" class="btn btn-add">
          <font-awesome-icon :icon="['fas', 'plus']" /> Nouveau inscription
        </router-link>

      </div>
     
      <div class="search-form">
         <input type="text" v-model="searchQuery" placeholder="Rechercher  élève ..." />
         <font-awesome-icon :icon="['fas', 'magnifying-glass']" class="icon"/>
      </div>
      <div class="table-responsive">
      <table class="table">
          <thead>
            <tr>
              <th>N°</th>
              <th>Date Inscription</th>
              <th>Etat</th>
              <th>Nom</th>
              <th>Prénoms</th>
              <th>Ligne</th>
              <th>Lieu de Ramassage</th>
              <th>Ecole</th>
              <th>Classe</th>
              <th>Date de naissance</th>
              <th>Image</th>
              <th>Actions</th>
            </tr>
          </thead>
          <transition-group name="eleve-table" tag="tbody">
            <tr v-for="eleve in eleves" :key="eleve.pk">
              <td>{{ eleve.pk }}</td>
              <td>{{ eleve.date_inscription }}</td>
              <th>{{eleve.etat}}</th>
              <td>{{ eleve.nom }}</td>
              <td>{{ eleve.prenoms }}</td>
              <td>{{ eleve.ligne }}</td>
              <td>{{ eleve.lieu_ramassage }}</td>
              <td>{{ eleve.ecole }}</td>
              <td>{{ eleve.classe }}</td>
              <td>{{ eleve.date_naissance }}</td>
              <td><img :src="eleve.image" alt=""></td>
              <td>
                
                <router-link :to="`/transport_scolaire/eleve/${$encryptId(eleve.pk)}/edit`" class="btn-action">
                                    <font-awesome-icon :icon="['fas', 'file-pen']" />
                </router-link>
                <router-link :to="`/transport_scolaire/eleve/${$encryptId(eleve.pk)}/delete`" class="btn-action" >
                  <font-awesome-icon :icon="['fas', 'x']" />
                </router-link> 
              </td>
            </tr>
          </transition-group>
      </table>
    </div>
      
     <div class="popup-buttons">
          
      </div>
    </div>
    
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'ElevePage',
  props: {
        baseUrl: String
      },
  data() {
    return {
      baseUrlPage:`${this.$store.state.baseUrl}/api/transport_scolaire/eleves/`,

      eleves: [],
     
      searchQuery: '',
      startDate: '',
      endDate: '',
      current_page:2,
      nbr_page:10,
    };
  },
  computed: {
   
    },

  methods: {

    fecthData(page){
              // Effectuer la requête GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: true, // Valeur de recherche
                } 
                })
                .then(response => {
                   const data = response.data;
                   this.eleves = data.results
                })
                .catch(error => {
                     console.error("Une erreur s'est produit lors de la récupération des élèves:", error);
                });
          },
  },

  mounted() {
    this.fecthData(this.baseUrlPage)
    }
};
</script>

<style scoped>

.eleve-table-enter {
  transition: transform 1s;
}
</style>