<template>
  <header :class="{'active':headerActive}">
    <ul class="circles">
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
              <li></li>
      </ul>
    <div class="wrapper">
        <div class="logo-nav">
            <div class="logo"  @click="handleMenuClick">
                 <font-awesome-icon :icon="['fas', 'bars']" />
                  <span>Vue Js</span>
            </div>
            
            <nav>
                 <div class="profil" @click="toggleUserInfo">
                     <div class="username">
                      <span v-if="$store.state.user">{{ $store.state.user.username}}</span>
                      <span v-if="!$store.state.user">Anonymous</span>
                          
                     </div>
                     <div class="photo username">
                          <img src="../assets/user.png" class="username" alt="">
                     </div>
                     <font-awesome-icon :icon="['fas', 'chevron-down']" class="username" />
                 </div>
            </nav>
            
        </div>
    </div>
    <div class="profil-info" v-if="isShowUserInfo">
          <div class="email-info" v-if="$store.state.user">
               <span>{{ $store.state.user.username}}</span>
               <span>{{ $store.state.user.email}}</span> 
          </div>
          <div class="email-info" v-if="!$store.state.user">
            <span>Anonymous 2 </span>
            <span>anonymous@mail.com </span> 
         </div>
          <router-link to="/eleve/nouveau" class="btn btn-logout">
           
            <font-awesome-icon :icon="['fas', 'key']" /> <span>Changer le mots de passe</span>
          </router-link>
          <button class="btn btn-logout" @click="logout">
           
                 <font-awesome-icon :icon="['fas', 'power-off']" /> <span> Se déconnecter</span>
          </button>
    </div>
  </header>
</template>

<script>

import { mapState } from 'vuex';

export default {
  name: 'HeaderComponent',
  computed: {
    ...mapState(['user'])
  },
  data(){
      return{
        isShowUserInfo:false,
        headerActive:false
      };
  },
  props: {
    msg: String
  },
  mounted() {
    this.handleWindowSizeChange()
    document.addEventListener('click', this.handleClickOutside);
  },
  methods:{
    handleMenuClick() {
   
        this.$emit('menu-click');
      
    },
    handleWindowSizeChange() {
   
      this.$emit('mounted');
    
    },
    toggleUserInfo() {
      this.isShowUserInfo = !this.isShowUserInfo;
    },
    logout() {
         localStorage.setItem('access','');
         this.$store.commit('setUser',null);
         this.$router.push('/');
    },
    handleClickOutside(event) {
      try{
      if (this.isShowUserInfo && !this.$el.contains(event.target)) {
            this.isShowUserInfo = false;
          }
      }catch(error){
          console.log()
      }
    
    }
  }
    
}
</script>

