
<template>
    <div class="container">
      
      <form @submit.prevent="editEcole" class="row form-form form-75">

        <div class="title">
          <h4>
            <span>Modification</span> <span>ecole</span>
          </h4>
          <div class="line">
          
          </div>
        </div>

        <h5>Informations  ecole</h5>
        <div class="col-12   col-md-6">
          <b-form-group label="nom :">
            <b-form-input id="nom" v-model="edit_ecole.nom" required></b-form-input>
          </b-form-group>
        </div>
        <h5>Classe Ecole</h5>
        <div class="table-responsive">
           <table class="table">
              <thead>
                  <tr>
                      <th>N°</th><th>Nom Classe</th><th>Actions</th>
                  </tr>
                  
              </thead>
              <tbody>
                <tr v-for="(classe,idx) in edit_ecole.classes" :key="idx">
                  <td>
                      {{idx+1}}
                  </td>
                  <td>
                    <b-form-input id="nom" v-model="edit_ecole.classes[idx].nom" required></b-form-input>
                  </td>
                  <td>
                    <font-awesome-icon @click="handleClasseDelete(idx,$event)" :icon="['fas', 'x']" />
                  </td>
               </tr>
              </tbody>
           </table>
          <b-button @click="handleClickBtnAddClasse($event)">
            <span>Ajouter Une Ligne</span>
            <font-awesome-icon  :icon="['fas', 'plus']" />
          </b-button>
        </div>
      
     
    
        <b-button type="submit" variant="" class="btn btn-save">
          <font-awesome-icon :icon="['fas', 'floppy-disk']" />
          <span>Enregistrer</span> 
        </b-button>
      
      </form>
    </div>
  </template>
  
  <script>
  import axios from 'axios';

  
 export default {
    name: 'ecoleCreatePage',
    mounted() {
         const encryptedId = this.$route.params.id; // Récupère l'ID crypté depuis l'URL
        const decryptedId = this.$decryptId(encryptedId); // Décrypte l'ID
        console.log(decryptedId);

        this.url=`${this.$store.state.baseUrl}/api/transport_scolaire/ecoles/${decryptedId}/`
        // console.log(url)
        this.fecthData(this.url)
    },
    props: {
         baseUrl: String
    },
   data() {
      return {

        baseUrlPage:`${this.$store.state.baseUrl}/api/transport_scolaire/ecoles/`,
          edit_ecole: {
            nom: '',
            classes:[]
  
        },

       };
    },
    methods: {
      
      handleClasseDelete(idx,event){
          idx,event
          this.edit_ecole.classes.splice(idx,1)
      },
      handleClickBtnAddClasse(event){
        event
        // event.preventDefault()
        const classe={
               nom_classe:''
          }
          console.log(this.edit_ecole.classes.length)
          this.edit_ecole.classes.push(classe)
      },
       fecthData(page){
              // Effectuer la requête GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: true, // Valeur de recherche
                } 
                })
                .then(response => {
                   const data = response.data;
                   this.edit_ecole = {
                    ...data,
                   }
                })
                .catch(error => {
                     console.error("Une erreur s'est candidate lors de la récupération des ecoles:", error);
                });
          },
      editEcole() {


          // Envoyer la requête PUT à l'API avec les données du ecole
          axios.put(this.url, this.edit_ecole)
                .then(response => {
                // Traitez la réponse de l'API en conséquence
                console.log(response.data);
                     // Réinitialisez les valeurs du formulaire

                this.$router.push('/transport_scolaire/ecole');
                })
                .catch(error => {
                // Gérez les erreurs de la requête
                console.error(error);
                });
     }
    },




  }
</script>

