
<template>
    <div class="container">
      
      <form @submit.prevent="addecole" class="row form-form form-75">

        <div class="title">
          <h4>
            <span>Ajout</span> <span>ecole</span>
          </h4>
          <div class="line">
          
          </div>
        </div>

        <h5>Informations  ecole</h5>
        <div class="col-12   col-md-6">
          <b-form-group label="nom :">
            <b-form-input id="nom" v-model="new_ecole.nom" required></b-form-input>
          </b-form-group>
        </div>
        <h5>Classe Ecole</h5>
        <div class="table-responsive">
           <table class="table">
              <thead>
                  <tr>
                      <th>N°</th><th>Nom Classe</th><th>Actions</th>
                  </tr>
                  
              </thead>
              <tbody>
                <tr v-for="(classe,idx) in new_ecole.classes" :key="idx">
                  <td>
                      {{idx+1}}
                  </td>
                  <td>
                    <b-form-input id="nom" v-model="new_ecole.classes[idx].nom" required></b-form-input>
                  </td>
                  <td>
                    <font-awesome-icon @click="handleClasseDelete(idx,$event)" :icon="['fas', 'x']" />
                  </td>
               </tr>
              </tbody>
           </table>
          <b-button @click="handleClickBtnAddClasse($event)">
            <span>Ajouter Une Ligne</span>
            <font-awesome-icon  :icon="['fas', 'plus']" />
          </b-button>
        </div>
      
     
    
        <b-button type="submit" variant="" class="btn btn-save">
          <font-awesome-icon :icon="['fas', 'floppy-disk']" />
          <span>Enregistrer</span> 
        </b-button>
      
      </form>
    </div>
  </template>
  
  <script>
  import axios from 'axios';

  
 export default {
    name: 'ecoleCreatePage',
    mounted() {
        //   this.maFonction("created");
        for (let index = 0; index <3; index++) {
          const classe={
               nom_classe:''
          }
          this.new_ecole.classes.push(classe)
          
        }
    },
    props: {
         baseUrl: String
    },
   data() {
      return {

        baseUrlPage:`${this.$store.state.baseUrl}/api/transport_scolaire/ecoles/`,
          new_ecole: {
            nom: '',
            classes:[]
  
        },

       };
    },
    methods: {
      
      handleClasseDelete(idx,event){
          idx,event
          this.new_ecole.classes.splice(idx,1)
      },
      handleClickBtnAddClasse(event){
        event
        // event.preventDefault()
        const classe={
               nom_classe:''
          }
          console.log(this.new_ecole.classes.length)
          this.new_ecole.classes.push(classe)
      },
      addecole() {


            // Créez un nouvel objet Project avec les valeurs du formulaire



            // Envoyer la requête POST à l'API avec les données du projet
            axios.post(this.baseUrlPage, this.new_ecole)
                .then(response => {
                // Traitez la réponse de l'API en conséquence
                console.log(response.data);


                this.$router.push('/transport_scolaire/ecole');
                })
                .catch(error => {
                // Gérez les erreurs de la requête
                   console.error(error);
                });
      
      
   
       }
  
    },




  }
</script>

