<template>
  <div>
    
    
    
    <div class="cont-sep">
      <div class="title">
        <h4>
             <span>Liste</span> <span>Elèves</span>
        </h4>
        <div class="line">
         
        </div>
      </div>
      <div class="actions">
        <router-link to="/transport_scolaire/eleve/nouveau" class="btn btn-add">
          <font-awesome-icon :icon="['fas', 'plus']" /> Ajouter un nouveau élève
        </router-link>

      </div>
      <div class="filter-form">
          <div class="item-group">
              <div class="item">
                <label>Date de début:</label>
                <input type="date" v-model="startDate" />
            </div>
              <div class="item">
                <label>Date de fin:</label>
                <input type="date" v-model="endDate" />
              </div>
              <div class="item">
                <label>Ligne:</label>
                <select name="" id="">
                  <option value="">-------------------</option>
                </select>
              </div>
          </div>
          <font-awesome-icon :icon="['fas', 'filter']" class="icon"/>

      </div>
      <div class="search-form">
         <input type="text" v-model="searchQuery" placeholder="Rechercher  élève ..." />
         <font-awesome-icon :icon="['fas', 'magnifying-glass']" class="icon"/>
      </div>
      <div class="table-responsive">
      <table class="table">
          <thead>
            <tr>
              <th>N°</th>
              <th>Date Inscription</th>
              <th>Nom</th>
              <th>Prénoms</th>
              <th>Ligne</th>
              <th>Lieu de Ramassage</th>
              <th>Ecole</th>
              <th>Classe</th>
              <th>Date de naissance</th>
              <th>Image</th>
              <th>Actions</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="eleve in eleves" :key="eleve.pk">
              <td>
              <router-link :to="`/transport_scolaire/eleve/${$encryptId(eleve.pk)}/detail`" class="btn-action">
                  {{ eleve.pk }}
              </router-link>

              </td>
              <td>{{ eleve.date_inscription }}</td>
              <td>{{ eleve.nom }}</td>
              <td>{{ eleve.prenoms }}</td>
              <td>{{ eleve.ligne }}</td>
              <td>{{ eleve.lieu_ramassage }}</td>
              <td>{{ eleve.ecole }}</td>
              <td>{{ eleve.classe }}</td>
              <td>{{ eleve.date_naissance }}</td>
              <td><img :src="eleve.image" alt=""></td>
              <td>
                
                <router-link :to="`/transport_scolaire/eleve/${$encryptId(eleve.pk)}/edit`" class="btn-action">
                                    <font-awesome-icon :icon="['fas', 'file-pen']" />
                </router-link>
                <router-link :to="`/transport_scolaire/eleve/${$encryptId(eleve.pk)}/delete`" class="btn-action" >
                  <font-awesome-icon :icon="['fas', 'x']" />
                </router-link> 
              </td>
            </tr>
          </tbody>
      </table>
    </div>
      <div class="pagination">
            <div class="page">
               <span>Page</span>  <span>{{currentPage}}</span> sur <span>{{ nbrPage }}</span>
            </div>
            <div class="btns">
                      <button @click="goToPage(previousPage)" :disabled="!previousPage" class="btn">
                        <font-awesome-icon :icon="['fas', 'chevron-left']" />
                    </button>
                    <button @click="goToPage(previousPage)"  class="btn">
                      {{currentPage}}
                    </button>
                    <button @click="goToPage(nextPage)" :disabled="!nextPage" class="btn">
                      <font-awesome-icon :icon="['fas', 'chevron-right']" />
                </button>
            </div>
            <div class="ads btn"  >
                .
            </div>
           
         </div>
     <div class="popup-buttons">
          
      </div>
    </div>
    
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'ElevePage',
  props: {
        baseUrl: String
      },
  data() {
    return {
      baseUrlPage:`${this.$store.state.baseUrl}/api/transport_scolaire/eleves/`,

      eleves: [],
      count:null,
      previousPage:null,
      nextPage:null,
      searchQuery: '',
      startDate: '',
      endDate: '',
      currentPage:1,
      nbrPage:10,
    };
  },
  computed: {
   
    },

  methods: {

    fecthData(page){
              // Effectuer la requête GET vers l'API en utilisant Axios
              axios.get(page,{ params: {
                    paginate: true, // Valeur de recherche
                     search_filter_etat:"validé",
                } 
                })
                .then(response => {
                   const data = response.data;
                   this.eleves = data.results


                this.count=data.count
                  // Mettre à jour les liens de pagination
                this.nextPage = data.next;
                this.previousPage = data.previous;

              // Obtenir le numéro de la page actuelle à partir de l'URL
                  let url;
                  if (data.next) {
                      url = new URL(data.next);
                      console.log("test1")
                  } else if (data.previous) {
                      url = new URL(data.previous);
                      console.log("test2")
                  } else {
                    // URL par défaut si aucun lien de pagination n'est disponible
                    url = new URL(this.baseUrlPage);
                    console.log("test")
                  }
                  url=new URL(page)
                  const searchParams = new URLSearchParams(url.search);
                  this.currentPage = parseInt(searchParams.get('page'));

                  if(isNaN(this.currentPage)){
                    this.currentPage=1;
                  }

                  console.log("Page actuelle :", this.currentPage);

                })
                .catch(error => {
                     console.error("Une erreur s'est produit lors de la récupération des élèves:", error);
                });
          },


          goToPage(page) {
             
             // Effectuer la requête GET vers l'API en utilisant Axios
             console.log(page)
           this.fecthData(page);
       },
  },

  mounted() {
    this.fecthData(this.baseUrlPage)
    }
};
</script>

<style scoped>

.eleve-table-enter {
  transition: transform 1s;
}
</style>