
<template>
  <div class="container">
    
    <form @submit.prevent="editEleve" class="row form-form form-75">

      <div class="title">
        <h4>
          <span>Modification</span> <span>Elève</span>
        </h4>
        <div class="line">
        
        </div>
      </div>

      <h5>Informations  Elève</h5>
      <div class="col-12   col-md-6">
        <b-form-group label="nom :">
          <b-form-input id="nom" v-model="new_eleve.nom" required></b-form-input>
        </b-form-group>
      </div>
      <div class="col-12 col-md-6">
        <b-form-group label="Prénom :">
          <b-form-input id="prenoms" v-model="new_eleve.prenoms" required></b-form-input>
