          <b-form-input id="date_naissance" v-model="new_eleve.date_naissance" type="date" required></b-form-input>
        </b-form-group>
      </div>
      <div class="col-12 col-md-6">
        <b-form-group label="Ecole :">
          <b-form-select v-model="new_eleve.ecole" :options="ecoles" required></b-form-select>
        </b-form-group>
      </div>
      <div class="col-12 col-md-6">
        <b-form-group label="Classe :">
          <b-form-select v-model="new_eleve.classe" :options="classes" required></b-form-select>
        </b-form-group>
      </div>
      <div class="col-12">
        <b-form-group label="Adresse :">
          <b-form-input v-model="new_eleve.adresse" required></b-form-input>
        </b-form-group>
      </div>
      <div class="col-12 col-md-6">
        <b-form-group label="Ligne de bus :">
          <b-form-select v-model="new_eleve.ligne_bus" :options="lignes_bus" required></b-form-select>
        </b-form-group>
      </div>
      <div class="col-12 col-md-6">
        <b-form-group label="Lieu de ligne :">
          <b-form-select v-model="new_eleve.lieu_ligne" :options="lieux_ligne" required></b-form-select>
        </b-form-group>
      </div>
    
      <div class="col-12">
        <b-form-group label="Lieu de ramassage :">
          <b-form-select v-model="new_eleve.lieu_ramassage" :options="lieux_ramassage" required></b-form-select>
        </b-form-group>
      </div>
      <h5>Informations Parent Elève</h5>
    <div class="col-12 col-md-6">
        <b-form-group label="Nom Père :">
          <b-form-input v-model="new_eleve.nom_pere" required></b-form-input>
        </b-form-group>
    </div>
    <div class="col-12 col-md-6">
    
        <b-form-group label="Tel :">
          <b-form-input v-model="new_eleve.tel_pere" required></b-form-input>
        </b-form-group>
    </div>
    <div class="col-12 col-md-6">
        <b-form-group label="Nom Mère :">
          <b-form-input v-model="new_eleve.nom_pere" required></b-form-input>
        </b-form-group>
    </div>
    <div class="col-12 col-md-6">
      <b-form-group label="Tel :">
        <b-form-input v-model="new_eleve.tel_pere" required></b-form-input>
      </b-form-group>
    </div>
   
  
      <b-button type="submit" variant="" class="btn btn-save">
        <font-awesome-icon :icon="['fas', 'floppy-disk']" />
        <span>Enregistrer les modifications</span> 
      </b-button>
    
    </form>
  </div>
</template>

<script>
import axios from 'axios';
const baseUrl="localhost:8000"

export default {
  name: 'EleveCreatePage',
  mounted() {
      //   this.maFonction("created");
  },

  data() {
  return {
    new_eleve: {
      nom: 'Doe',
      prenoms: 'John',
      date_naissance: '2005-08-15',
      ecole: 2,
      niveau: 'Niveau 3',
      classe: 3,
      ligne_bus: 1,
      lieu_ligne: 'Lieu 2',
      lieu_ramassage: 'Lieu 1',
      nom_pere: 'Doe',
      tel_pere: '0123456789',
      nom_mere: 'Smith',
      tel_mere: '9876543210',
      adresse: '123 Rue Principale'
    },
    ecoles: [
      { value: 1, text: 'Ecole 1' },
      { value: 2, text: 'Ecole 2' },
      { value: 3, text: 'Ecole 3' }
    ],
    classes: [
      { value: 1, text: 'Classe 1' },
      { value: 2, text: 'Classe 2' },
      { value: 3, text: 'Classe 3' }
    ],
    lignes_bus: [
      { value: 1, text: 'Ligne 1' },
      { value: 2, text: 'Ligne 2' },
      { value: 3, text: 'Ligne 3' }
    ],
    lieux_ligne: [
      { value: 'Lieu 1', text: 'Lieu 1' },
      { value: 'Lieu 2', text: 'Lieu 2' },
      { value: 'Lieu 3', text: 'Lieu 3' }
    ],
    lieux_ramassage: [
      { value: 'Lieu 1', text: 'Lieu 1' },
      { value: 'Lieu 2', text: 'Lieu 2' },
      { value: 'Lieu 3', text: 'Lieu 3' }
    ]
  };
},
  methods: {
     
    editEleve() {


          // Créez un nouvel objet Project avec les valeurs du formulaire
          const new_eleve = {
              nom: this.new_eleve.nom,
              agge: this.new_eleve.agge,
          };
          this.$router.push('/eleve');


          // Envoyer la requête POST à l'API avec les données du projet
          axios.post(`http://${baseUrl}/api/eleve`, new_eleve)
              .then(response => {
              // Traitez la réponse de l'API en conséquence
              console.log(response.data);
                   // Réinitialisez les valeurs du formulaire
        this.new_eleve.nom = null ;
        this.new_eleve.agge = null ;

              this.$router.push('/Eleve');
              })
              .catch(error => {
              // Gérez les erreurs de la requête
              console.error(error);
              });
    
    
 
     }

  },




}
</script>

