<template>
  <div>
    
    
    
    <div class="cont-sep">
      <div class="title">
        <h4>
             <span>Liste</span> <span>Lignes Bus</span>
        </h4>
        <div class="line">
         
        </div>
      </div>
      <div class="actions">
        <router-link to="/transport_scolaire/ligne/nouveau" class="btn btn-add">
          <font-awesome-icon :icon="['fas', 'plus']" /> Ajouter un nouveau ligne bus
        </router-link>

      </div>
      <div class="filter-form">
          

      </div>
      <div class="search-form">
         <input type="text" v-model="searchQuery" placeholder="Rechercher  élève ..." />
         <font-awesome-icon :icon="['fas', 'magnifying-glass']" class="icon"/>
      </div>
      <div class="table-responsive">
      <table class="table">
          <thead>
            <tr>
              <th>N°</th>
              <th>Nom</th>
              <th>Lieux</th>
              <th>Actions</th>
            </tr>
          </thead>
          <transition-group name="ligne-table" tag="tbody">
            <tr v-for="ligne in lignes" :key="ligne.id">
              <td>{{ ligne.pk }}</td>
              <td>{{ ligne.nom }}</td>
              <td>
                <span v-for="odrelieu in ligne.ordrelieus" :key="odrelieu.pk">
                  --{{ odrelieu.lieu_ligne.repr }}
                </span>
            </td>
              <td>
                
                <router-link :to="`/transport_scolaire/ligne/${$encryptId(ligne.pk)}/edit`" class="btn-action"> 
                    <font-awesome-icon :icon="['fas', 'file-pen']" />
                  </router-link>
                <router-link :to="`/transport_scolaire/ligne/${$encryptId(ligne.pk)}/delete`" class="btn-action" >
                  <font-awesome-icon :icon="['fas', 'x']" />
                </router-link> 

              </td>
            </tr>
          </transition-group>
      </table>
    </div>
      <div class="pagination">
            <div class="page">
               <span>Page</span>  <span>{{current_page}}</span> sur <span>{{ nbr_page }}</span>
            </div>
            <div class="btns">
                      <button @click="goToPageCandidat(previousPageCandidat)" :disabled="!previousPageCandidat" class="btn">
                        <font-awesome-icon :icon="['fas', 'chevron-left']" />
                    </button>
                    <button @click="goToPageCandidat(previousPageCandidat)"  class="btn">
                      {{current_page}}
                    </button>
                    <button @click="goToPageCandidat(nextPageCandidat)" :disabled="!nextPageCandidat" class="btn">
                      <font-awesome-icon :icon="['fas', 'chevron-right']" />
                </button>
            </div>
            <div class="ads btn"  >
                .
            </div>
           
         </div>
     <div class="popup-buttons">
          
      </div>
    </div>
    
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'LignePage',
  data() {
    return {
      baseUrlPage:`${this.$store.state.baseUrl}/api/transport_scolaire/lignes/`,
      lignes: [],
      searchQuery: '',
      startDate: '',
      endDate: '',
      current_page:2,
      nbr_page:10,
    };
  },
  computed: {
   
  },
  methods: {
      fecthData(page){
                // Effectuer la requête GET vers l'API en utilisant Axios
                axios.get(page,{ params: {
                      paginate: true, // Valeur de recherche
                      special_detail_manytoone_class:"['ordrelieus',]"
                  } 
                  })
                  .then(response => {
                    const data = response.data;
                    console.log(data)
                    this.lignes = data.results
                  })
                  .catch(error => {
                      console.error("Une erreur s'est produit lors de la récupération des lignes:", error);
                  });
            },
     },
     mounted() {
       this.fecthData(this.baseUrlPage)
    }
};
</script>

<style scoped>

.ligne-table-enter {
  transition: transform 1s;
}
</style>